import React, { Fragment, Component } from "react";
import classnames from "classnames";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import style from "./style.scss";
import { Fa } from "mdbreact";
import { hoverNavbar } from "store/Navbar/action";
import { navbarData } from "../../data";
import Subnav from "./Subnav";

class Index extends Component {
  render() {
    const {
      hoverNavbar,
      isShowNav,
      navIndex,
      isShowNavSub,
      navSubIndex
    } = this.props;
    return (
      <Fragment>
        {navbarData.map((nav, index) => (
          <div
            onMouseEnter={() => {
              hoverNavbar({
                navIndex: index,
                isShowNav: true
              });
            }}
            onMouseLeave={() => {
              hoverNavbar({
                navIndex: null,
                isShowNav: false
              });
            }}
            className="position-relative"
            key={index}
          >
            {/**  */}
            <Link to={nav.url}>
              <div
                className={classnames(
                  "d-flex justify-content-between p-3",
                  style.navLink,
                  isShowNav && navIndex === index && style.navItemHover1
                )}
              >
                <div>{nav.name}</div>
                {nav.subnav && (
                  <div className="ml-3">
                    <Fa icon="caret-down" />
                  </div>
                )}
              </div>
            </Link>

            <div>
              {isShowNav && navIndex === index && (
                <Subnav
                  navUrl={nav.url}
                  navIndex={navIndex}
                  isShowNavSub={isShowNavSub}
                  navSubIndex={navSubIndex}
                  subnav={nav.subnav}
                  hoverNavbar={hoverNavbar}
                />
              )}
            </div>
          </div>
        ))}
      </Fragment>
    );
  }
}

const connectIndex = connect(
  state => {
    const { isShowNav, navIndex, isShowNavSub, navSubIndex } = state.navbar;
    return {
      isShowNav,
      navIndex,
      isShowNavSub,
      navSubIndex
    };
  },
  { hoverNavbar }
)(Index);

export default connectIndex;
