import React, { Fragment } from "react";
import { navbarData } from "../../data";
import { Link, withRouter } from "react-router-dom";
import { checkCurrentUrl } from "util/Common";
import style from "./style.scss";
import classnames from "classnames";
import { FaTimes } from "react-icons/fa";
import { Button } from "mdbreact";
import { connect } from "react-redux";
import AuthNav from "./AuthNav";

const Index = ({ isAuth, history, match: { url }, onToggleSidebar }) => (
  <div>
    <div
      className="position-absolute right-0 p-2 text-success"
      onClick={() => onToggleSidebar()}
    >
      <FaTimes size={25} />
    </div>
    <div className={classnames(style.navbarResponsive, "text-black-70")}>
      {navbarData.map((nav, i) => (
        <div
          key={i}
          onClick={() => {
            history.push(nav.url);
          }}
          className={classnames(
            "p-3 font-weight-normal",
            checkCurrentUrl(nav.url, url) && style.activeItemResponsive
          )}
        >
          {nav.name}
        </div>
      ))}
      <hr />
      {isAuth ? (
        <AuthNav />
      ) : (
        <Fragment>
          <Link to="/login">
            <div
              className={classnames(
                style.navItemResponsive,
                "p-3 font-weight-normal"
              )}
            >
              Log in
            </div>
          </Link>
          <Link to="/sign-up">
            <Button color="success" className="w-100">
              SIGN UP
            </Button>
          </Link>
        </Fragment>
      )}
    </div>
  </div>
);
const connectIndex = connect(
  ({ auth: { isAuth } }) => ({ isAuth }),

  undefined
)(Index);
export default withRouter(connectIndex);
