import React, { Fragment } from "react";
export default ({ title, jobs }) => (
  <Fragment>
    <div className="font-weight-bold text-uppercase">{title}</div>
    <hr />
    <div className="mb-5">
      {jobs.map((job, i) => (
        <div key={i}>
          <div className="font-weight-500">{job.title}</div>
          <div className="font-size-13 font-weight-light my-3">{job.desc}</div>
        </div>
      ))}
    </div>
  </Fragment>
);
