import React, { Fragment, PureComponent } from "react";
import { Fa } from "mdbreact";
import classNames from "classnames";
import { Collapse } from "mdbreact";
import Lecture from "./Lecture";
import { withRouter } from "react-router-dom";
import { Droppable, Draggable } from "react-beautiful-dnd";
import {
  setCurrentSection,
  deleteSectionRequest,
  toggleSection
} from "store/Instructor/action/section";
import { setCurrentLecture } from "store/Instructor/action/lecture";
import { connect } from "react-redux";
import style from "./style.scss";
import { randomString } from "util/Common";
import { FaRegTrashAlt } from "react-icons/fa";

class Index extends PureComponent {
  handleDelete = id => {
    const { deleteSectionRequest, currentCourse, history } = this.props;
    deleteSectionRequest({ id, currentCourse, history });
  };
  render() {
    const {
      collapse,
      index,
      section,
      lectures,
      setCurrentSection,
      setCurrentLecture,
      currentLecture,
      currentSection,
      history,
      toggleSection
    } = this.props;
    const defaultLecture = {
      id: randomString(),
      title: "No lecture",
      seen: true
    };
    const sectionId = section.id;
    const courseId = localStorage.getItem("courseId");
    const collapseSection = collapse ? collapse[sectionId] : false;
    const canDelete = section.lectures.length === 0;
    return (
      <Draggable draggableId={`section-${section.id}`} index={index}>
        {provided => (
          <Fragment>
            <div
              {...provided.draggableProps}
              {...provided.dragHandleProps}
              ref={provided.innerRef}
              onClick={() => {
                toggleSection(section.id);
                setCurrentSection(section);
                !!currentLecture && setCurrentLecture(null);
                history.push(
                  `/instructor/edit/${courseId}/content/section/${sectionId}`
                );
              }}
            >
              <div
                className={classNames(
                  "d-flex justify-content-between cursor-pointer ease-in-out box-shadow-hover bg-white mt-3",
                  currentSection &&
                    currentSection.id === sectionId &&
                    style.itemActive
                )}
              >
                <div className="d-flex px-4 py-3">
                  {/** section  */}
                  <div>
                    <Fa
                      className="text-primary jus"
                      icon={collapseSection ? "folder" : "folder-o"}
                    />
                  </div>
                  <div
                    className={classNames(
                      "ml-2 font-weight-normal text-primary",
                      collapseSection && "font-weight-bold "
                    )}
                  >
                    Section {index + 1}: {section.title}
                  </div>
                  {/** section  */}
                </div>
                <div className="d-flex ">
                  <div
                    className="cursor-pointer px-2 py-3 mt-1 text-primary"
                    onClick={() => this.handleDelete(section.id)}
                  >
                    {canDelete && <FaRegTrashAlt />}
                  </div>
                  {/** collapse */}
                  <div className="text-primary ml-auto px-3 py-3">
                    {collapseSection ? (
                      <Fa icon={"caret-up"} />
                    ) : (
                      <Fa icon={"caret-down"} />
                    )}
                  </div>
                </div>
              </div>
            </div>

            <Droppable droppableId={`section-${sectionId}`}>
              {provided => (
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  <Collapse isOpen={collapseSection}>
                    {lectures.length !== 0 ? (
                      lectures.map((lecture, index) => (
                        <Lecture
                          key={index}
                          section={section}
                          index={index}
                          lecture={lecture}
                        />
                      ))
                    ) : (
                      <Lecture
                        key={index}
                        index={index}
                        lecture={defaultLecture}
                      />
                    )}
                    {provided.placeholder}
                  </Collapse>
                </div>
              )}
            </Droppable>
          </Fragment>
        )}
      </Draggable>
    );
  }
}

const connectIndex = connect(
  ({
    instructor: { currentCourse, currentSection, currentLecture, collapse }
  }) => ({
    currentLecture,
    currentSection,
    currentCourse,
    collapse
  }),
  { setCurrentSection, setCurrentLecture, deleteSectionRequest, toggleSection }
)(Index);

export default withRouter(connectIndex);
