import React, { Fragment, PureComponent } from "react";
import Stepper from "pages/Instructor/components/Stepper";
import CourseInfo from "pages/Instructor/components/CourseInfo";
import { getCourseRequest } from "store/Instructor/action/course";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

class Index extends PureComponent {
  componentDidMount() {
    const {
      match: {
        params: { id }
      },
      getCourseRequest
    } = this.props;
    getCourseRequest(id);
  }

  render() {
    const { currentCourse } = this.props;
    return (
      <Fragment>
        <div className="p-5">
          <Stepper currentStep={0} />
        </div>

        <div>
          <CourseInfo currentCourse={currentCourse} isEdit={true} />
        </div>
      </Fragment>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse } }) => ({
    currentCourse
  }),
  { getCourseRequest }
)(Index);

export default withRouter(connectIndex);
