import React from "react";
import { Link } from "react-router-dom";

export default ({ type, name, url }) => {
  let ShowName = <span>{name}</span>;
  let Url = "/not-implement";
  switch (type) {
    case 1:
      Url = `/experts${url}`;
      break;
    case 2:
      ShowName = <span>{name} experts help</span>;
      Url = `/search${url}`;
      break;
    case 3:
      ShowName = <span>learn {name} online</span>;
      break;
    default:
      ShowName = <span>{name}</span>;
      break;
  }

  return (
    <Link
      to={Url}
      className="text-capitalize font-weight-normal text-white-70 text-white-hover"
    >
      {ShowName}
    </Link>
  );
};
