import React, { Fragment } from "react";

export default ({ data, name, onClick, current }) => {
  return (
    <Fragment>
      {data.map((item, i) => {
        return (
          <div key={i} className="mb-2">
            <input
              checked={current === item.value}
              type="radio"
              name={name}
              value={item.value}
              onChange={() => onClick(item.value)}
            />
            <span className="ml-2 font-weight-500">{item.label}</span>
          </div>
        );
      })}
    </Fragment>
  );
};
