import React, { Component } from "react";
import {
  countryData,
  skillData,
  languageData,
  levelData,
  jobTypeData,
  jobTypeDefault
} from "../data/index";
import Select from "react-select";
import { Button } from "mdbreact";
import { Range } from "rc-slider";
import Clear from "./clear";
import makeAnimated from "react-select/lib/animated";
import defaultState from "./defaultState";
import { Checkbox, InputDate } from "components/Field/index";
import DatePicker from "react-datepicker";
import { FaMinus } from "react-icons/fa";

class Index extends Component {
  constructor() {
    super();
    this.state = defaultState;
  }

  handleChange = name => value => {
    this.setState({
      [name]: value
    });
  };

  handleClear = object => {
    this.setState({
      ...object
    });
  };

  handleChangeInputSlider = ({ target: { value } }, name, isMin = true) => {
    const min = isMin ? parseInt(value, 10) : this.state[name][0];
    const max = !isMin ? parseInt(value, 10) : this.state[name][1];
    this.setState({
      [name]: [min, max]
    });
  };

  handleChangeCheckBox = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  render() {
    const {
      pay,
      level,
      budget,
      skill,
      country,
      language,
      withoutPay,
      withoutBudget,
      newerThan
    } = this.state;

    const Title = ({ data }) => (
      <div className="font-weight-bold mt-3 mb-2">{data}</div>
    );
    console.log(this.state);
    return (
      <div className="left-side">
        <div className="d-flex justify-content-between align-items-center ">
          <div className="font-weight-bold text-uppercase">Filters</div>
          <div>
            <Button onClick={() => this.handleClear(defaultState)}>
              Clear all
            </Button>
          </div>
        </div>
        <hr />
        {/** skills */}
        <Title data="Skills" />
        <Select
          isMulti
          value={skill}
          components={makeAnimated()}
          name="skill"
          onChange={this.handleChange("skill")}
          options={skillData}
          className="basic-multi-select"
          classNamePrefix="select"
        />
        {/** end skilsl */}

        {/** job type */}
        <Clear
          title="Job type"
          callback={() => this.handleClear(jobTypeDefault)}
        />
        {jobTypeData.map((item, i) => (
          <Checkbox
            key={i}
            label={item.label}
            checked={this.state[`jobType${item.value}`] ? true : false}
            onChange={this.handleChangeCheckBox("jobType" + item.value)}
          />
        ))}
        {/** end job type */}

        {/* Newer than */}
        <Clear
          title="Newer than"
          callback={() => this.handleClear({ newerThan: null })}
        />
        <DatePicker
          name="newerThan"
          customInput={<InputDate />}
          selected={newerThan}
          onChange={this.handleChange("newerThan")}
          dateFormat="ddd, MMM DD YYYY"
        />
        {/** end Newer than */}

        {/** Pay rate */}
        <Clear
          title="Pay rate ($/hr)"
          callback={() =>
            this.handleClear({
              withoutPay: false,
              pay: [1, 100]
            })
          }
        />
        <input
          width="55"
          className="input-rage input-pay-rate"
          value={pay[0]}
          min={1}
          max={100}
          onChange={e => this.handleChangeInputSlider(e, "pay")}
        />
        <span className="mx-2">
          <FaMinus />
        </span>
        <input
          min={1}
          max={100}
          className="input-rage input-pay-rate"
          value={pay[1]}
          onChange={e => this.handleChangeInputSlider(e, "pay", false)}
        />
        <span className="ml-2">USD</span>
        <Range
          min={1}
          max={100}
          value={pay}
          className="my-3"
          onChange={this.handleChange("pay")}
        />
        <Checkbox
          checked={withoutPay}
          onChange={this.handleChangeCheckBox("withoutPay")}
          name="withoutPay"
          label="Include jobs without pay rates"
        />
        {/** end Pay rate */}

        {/** Budget */}
        <Clear
          title="Budget"
          callback={() =>
            this.handleClear({
              withoutBudget: false,
              budget: [1, 100000]
            })
          }
        />
        <input
          min={1}
          max={100000}
          className="input-rage input-budget"
          value={budget[0]}
          onChange={e => this.handleChangeInputSlider(e, "budget")}
        />
        <span className="mx-2">
          <FaMinus />
        </span>
        <input
          min={1}
          max={100000}
          className="input-rage input-budget"
          value={budget[1]}
          onChange={e => this.handleChangeInputSlider(e, "budget", false)}
        />
        <span className="ml-2">USD</span>
        <Range
          min={1}
          max={100000}
          value={budget}
          className="my-3"
          onChange={this.handleChange("budget")}
        />
        <Checkbox
          checked={withoutBudget}
          onChange={this.handleChangeCheckBox("withoutBudget")}
          label="Include jobs without budgets"
        />
        {/** end Budget */}

        {/** level */}
        <Title data="Experience level" />
        <Select
          name="level"
          onChange={this.handleChange("level")}
          options={levelData}
          defaultValue={levelData[0]}
          value={level}
          className="basic-multi-select"
          classNamePrefix="select"
        />
        {/** end level */}

        {/** country */}
        <Title data="Country" />
        <Select
          isMulti
          components={makeAnimated()}
          value={country}
          name="country"
          onChange={this.handleChange("country")}
          options={countryData}
          className="basic-multi-select"
          classNamePrefix="select"
        />
        {/** end country */}

        {/** language */}
        <Title data="Language" />
        <Select
          isMulti
          components={makeAnimated()}
          value={language}
          name="language"
          onChange={this.handleChange("language")}
          options={languageData}
          className="basic-multi-select"
          classNamePrefix="select"
        />
        {/** end language */}
      </div>
    );
  }
}

export default Index;
