import React from "react";
import style from "./style.scss";
import classNames from "classnames";
import { Link, withRouter } from "react-router-dom";
import { checkCurrentUrl } from "util/Common";

const Index = ({ match: { url }, data }) => {
  return (
    <div className="mb-3">
      <div className="font-weight-bold row">
        {data.map((item, i) => (
          <div key={i} className="col-12 col-md-4">
            <Link
              to={
                item.slug === "" ? `/course` : `/course/category/${item.slug}`
              }
            >
              <div
                className={classNames(
                  "px-3 py-2 mb-2 bg-white box-shadow-default",
                  style.item,
                  checkCurrentUrl(item.slug, url, "/course") &&
                    "bg-success text-white"
                )}
              >
                {item.name}
              </div>
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
};
export default withRouter(Index);
