export default ({ i, id, lectureId, urlBack, item }) => {
  let itemUrl;
  switch (i) {
    case 0:
      itemUrl = `/course/${id}/lecture/${lectureId}`;
      break;
    case 1:
    case 2:
      itemUrl = `/course/${id}${item.url}`;
      break;
    default:
      itemUrl = urlBack;
  }
  return itemUrl;
};
