import Header from "./components/Header";
import Footer from "./components/Footer";
import React, { Component } from "react";
import NProgress from "nprogress";
import { Helmet } from "react-helmet";
// eslint-disable-next-line
import style from "./style.scss";

NProgress.configure({
  minimum: 0.4,
  showSpinner: false
});

export default (App, noHeader = false) => {
  class Layout extends Component {
    componentWillUpdate() {
      NProgress.start();
    }

    componentDidUpdate() {
      const a = document.getElementById("jobContainer");
      a.click();
      NProgress.done();
      window.scrollTo(0, 0);
    }

    render() {
      if (noHeader) {
        return (
          <div id="jobContainer" style={{ paddingTop: "0" }}>
            <App />
          </div>
        );
      }

      return (
        <div id="jobContainer">
          <Helmet bodyAttributes={{ style: "background-color : #F4F7FA" }} />
          <Header />
          <div style={{ flexGrow: "1" }}>
            <App />
          </div>
          <Footer />
        </div>
      );
    }
  }

  return Layout;
};
