import React from "react";
import classnames from "classnames";
import Select from "react-select";
import makeAnimated from "react-select/lib/animated";

class SimpleSelect extends React.PureComponent {
    render() {
        const props = this.props;
        const isError = !!((!props.field || props.field.length === 0) && props.first);
        return (
            <div className={classnames('form-group', 'mb-3')}>
                <label className={isError ? 'error' : ''}
                       htmlFor={props.id}>{props.label}</label>
                <Select
                    isMulti={props.isMulti}
                    id={props.id}
                    value={props.field}
                    components={makeAnimated()}
                    maxMenuHeight={170}
                    placeholder={props.placeholder}
                    onChange={this.props.handleChange(props.id)}
                    options={props.options}
                    menuPlacement={'auto'}
                    styles={{
                        placeholder: (provided) => ({
                            ...provided,
                            color: '#7f92a9'
                        }),
                        control: (provided, state) => ({
                            ...provided,
                            borderColor: !state.isFocused ? (isError ? '#dc3545' : '#ced4da') : '#2684FF',
                            '&:hover': {
                                borderColor: !state.isFocused ? (isError ? '#dc3545' : '#ced4da') : '#2684FF',
                            }
                        }),
                        singleValue: (provided) => ({
                            ...provided,
                            color: '#495057'
                        })
                    }}
                    classNamePrefix="react-select"/>
                <div className={classnames({
                    'invalid-feedback': true,
                    'd-block': isError
                })}>{props.error}</div>
            </div>
        )
    }
}

SimpleSelect.defaultProps = {
    isMulti: false,
    error: 'Please provide a valid name.'
};

export default SimpleSelect;