import React from "react";
import classnames from "classnames";

class Input extends React.PureComponent {
    render() {
        const props = this.props;
        const input = props.unit ?
            <div className={'d-flex'}>
                <input style={{
                    flexGrow: 1,
                    width: 'auto',
                    borderTopRightRadius: '0',
                    borderBottomRightRadius: '0',
                }}
                       value={props.field} onChange={props.onChange}
                       type={props.type}
                       id={props.id} className="form-control" name={props.id}
                       placeholder={props.placeholder} required/>
                <div className={'mid-container font-size-14 blur'} style={{
                    border: '1px solid #ced4da',
                    padding: '0 4px',
                    borderLeft: 'none',
                    borderTopRightRadius: '0.25rem',
                    borderBottomRightRadius: '0.25rem',
                }}>
                    {props.unit}
                </div>
            </div> :
            <input value={props.field} onChange={props.onChange}
                   type={props.type}
                   id={props.id} className="form-control" name={props.id}
                   placeholder={props.placeholder} required/>;
        return (
            <div className={classnames('form-group', 'mb-3', props.className)}>
                <label className={!props.field && props.first ? 'error' : ''}
                       htmlFor={props.id}>{props.label}</label>
                {props.type === 'textarea' ?
                    <textarea rows={'5'} value={props.field} onChange={props.onChange}
                              id={props.id} className="form-control" name={props.id}
                              placeholder={props.placeholder} required/> : input
                }
                <div className={classnames({
                    "invalid-feedback": true,
                    'd-block': !!((props.unit && !props.field) && props.first)
                })}>{props.error}</div>
            </div>
        )
    }
}

Input.defaultProps = {
    className: '',
    placeholder: 'handsome boy',
    error: 'Please provide a valid name.',
    type: 'text'
};

export default Input;