import React, { Component, Fragment } from "react";
import Card from "components/WithMentor/Course/Card";
import { withRouter, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import Loading from "components/Loading";
import { getCourseRequest, enrollCourseRequest } from "store/Course/action";
import { SearchInput } from "components/Field";
import Footer from "components/WithMentor/Footer";
import { Button } from "mdbreact";

class Index extends Component {
  state = {
    search: ""
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  componentDidMount() {
    const {
      match: {
        params: { id }
      },
      course,
      getCourseRequest
    } = this.props;

    // eslint-disable-next-line
    if (!course || course.id != id) {
      getCourseRequest(id);
    }
  }

  handleSearch = () => {
    this.getCourses({
      page: 2,
      cate: ""
    });
  };

  enrollCourse = () => {
    const {
      enrollCourseRequest,
      match: {
        params: { id }
      },
      history
    } = this.props;
    enrollCourseRequest({ id, history });
  };

  render() {
    const {
      isLoading,
      course,
      match: {
        params: { id }
      }
    } = this.props;

    if (isLoading || !course) return <Loading />;

    if (course.join) {
      return <Redirect to={`/course/${id}/overview`} />;
    }
    return (
      <Fragment>
        <div className="text-center z-depth-1 position-relative">
          <div className="col-md-8 col-12 offset-md-2 col-xl-6 offset-xl-3">
            <SearchInput
              name="search"
              value={this.state.search}
              handleChange={this.handleChange}
              handleSearch={this.handleSearch}
            />
          </div>
        </div>
        <div className="container pt-5">
          <div>
            <h2 className="text-center font-weight-normal h2-responsive py-3">
              Would you like to enroll to the following course?
            </h2>
          </div>
          <div className="row my-5">
            <div className="col-4 offset-4 mt-md-0 mb-4">
              <Card data={course} isHover={false} />
            </div>
            <div className="col-4 offset-4 mt-md-0 mb-4">
              <Button
                color="danger"
                className="m-0 w-100"
                onClick={() => this.enrollCourse()}
              >
                Enroll
              </Button>
            </div>
          </div>
        </div>
        <Footer />
      </Fragment>
    );
  }
}

const connectIndex = connect(
  ({ course: { current: course, isLoading } }) => ({ course, isLoading }),
  { getCourseRequest, enrollCourseRequest }
)(Index);

export default withRouter(connectIndex);
