import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "mdbreact";
// import { Fa } from "mdbreact";
import withJob from "components/WithJob";
import { Helmet } from "react-helmet";
import Form from './Form';

// const socialButton = ["facebook-f", "youtube-play"];

class SignUp extends Component {
  render() {
    return (
      <div className={"width d-flex flex-column"}>
        <Helmet bodyAttributes={{ style: "background-color : #F4F7FA" }}>
          {" "}
          <title>Sign up</title>
        </Helmet>
        <div className={"mid-container "} style={{ padding: "30px 0" }}>
          <Link to="/" className="mid-container">
            <div
              style={{ fontSize: "2rem", paddingTop: "10px" }}
              className="ml-2 main-color logo-title"
            >
              Sign up now
            </div>
          </Link>
        </div>
        <div className={"width"}>
          <Container>
            <Row className={"mid-container mt-3"}>
              <Col xs={"12"} sm={"8"} lg={"6"}>
                <div className={"page py-3 px-4 "}>
                  <Form />
                </div>
                <div
                  className={"mid-container my-4 font-weight-500"}
                  style={{ fontSize: "0.85rem" }}
                >
                  Already have an account?
                  <Link to={"/login"} className={"pl-1 no-hover font-weight-500 text-green"}>
                    Sign in
                  </Link>
                </div>
              </Col>
            </Row>

            <Row className={"mb-3 blur"}>
              <Col xs={"12"} xl="4">
                <Link
                  to="/"
                  className="mid-container mt-2 text-center text-md-left"
                >
                  <div className="text-dark logo-title">Codementor</div>
                </Link>
              </Col>
              <Col
                xs={"12"}
                xl="4"
                className={"mid-container"}
                style={{ fontSize: "0.85rem", fontWeight: "400" }}
              >
                Find the world's best agencies and freelancers for FREE
              </Col>

              <Col xs={"12"} xl="4" align="right">
                <Link
                  to="/jobs"
                  className="mid-container mt-2 text-center text-md-right"
                >
                  <div className="main-color logo-title">Hello work</div>
                </Link>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    );
  }
}

export default withJob(SignUp, true);
