import { takeEvery, call, put, takeLatest } from "redux-saga/effects";
import {
  // action
  setCurrentLecture,
  updateLectureFailure,
  updateLectureSuccess,
  createLectureFailure,
  createLectureSuccess,
  getLectureSuccess,
  getLectureFailure,
  orderLectureFailure,
  orderLectureSuccess,
  deleteLectureFailure,
  deleteLectureSuccess,

  // type
  CREATE_LECTURE_REQUEST,
  UPDATE_LECTURE_REQUEST,
  SET_CURRENT_LECTURE,
  GET_LECTURE_REQUEST,
  ORDER_LECTURE_REQUEST,
  DELETE_LECTURE_REQUEST
} from "../action/lecture";
import { getCourseRequest } from "../action/course";
import { getSectionRequest, setCurrentSection } from "../action/section";
import {
  CreateLecture,
  UpdateLecture,
  GetLecture,
  OrderLecture,
  DeleteLecture,
  CreateVideo
} from "store/api/Version1";
import { removeItemById } from "util/Common";

function* watchSetCurrentLecture() {
  yield takeEvery(SET_CURRENT_LECTURE, setCurrentLecture);
}

//create
function* createLecture({
  payload: {
    sectionId,
    params: { type, title, newVideo, content },
    history
  }
}) {
  try {
    const courseId = localStorage.getItem("courseId");

    // get lecture
    // get lecture
    let lecture = { title, type };
    if (type === 1 && newVideo) {
      // call API and get url then update to db
      const params = {
        name: newVideo.name,
        size: newVideo.size,
        url: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
      };

      const { data } = yield call(CreateVideo, params);
      // update current lecture
      lecture = { ...lecture, videoId: data.id, video: data };
    } else {
      lecture = { ...lecture, content };
    }
    const { data } = yield call(CreateLecture, sectionId, lecture);
    yield put(createLectureSuccess(data));
    yield put(getCourseRequest(courseId));
    history.push(`/instructor/edit/${courseId}/content/lecture/${data.id}`);
  } catch (error) {
    yield put(createLectureFailure(error));
  }
}
function* watchCreateLecture() {
  yield takeLatest(CREATE_LECTURE_REQUEST, createLecture);
}

// get
function* getLecture({ payload: { id } }) {
  try {
    const { data } = yield call(GetLecture, id);
    yield put(getLectureSuccess(data));
    localStorage.setItem("sectionId", data.sectionId);
    yield put(getSectionRequest(data.sectionId));
  } catch (error) {
    yield put(getLectureFailure(error));
  }
}
function* watchGetLecture() {
  yield takeLatest(GET_LECTURE_REQUEST, getLecture);
}

// update
function* updateLecture({
  payload: {
    id,
    params: { type, title, newVideo, content }
  }
}) {
  try {
    const courseId = localStorage.getItem("courseId");
    // get lecture
    let lecture = { title, type };
    if (type === 1 && newVideo) {
      // call API and get url then update to db
      const params = {
        name: newVideo.name,
        size: newVideo.size,
        url: "http://media.w3.org/2010/05/bunny/movie.mp4"
      };

      const { data } = yield call(CreateVideo, params);
      // update current lecture
      lecture = { ...lecture, videoId: data.id, video: data };
    } else {
      lecture = { ...lecture, content };
    }

    yield call(UpdateLecture, id, lecture);
    yield put(updateLectureSuccess(lecture));
    yield put(getCourseRequest(courseId));
  } catch (error) {
    yield put(updateLectureFailure(error));
  }
}

function* watchUpdateLecture() {
  yield takeLatest(UPDATE_LECTURE_REQUEST, updateLecture);
}

// Order
function* orderLecture({ payload: { params } }) {
  try {
    yield call(OrderLecture, params);
    yield put(orderLectureSuccess());
  } catch (error) {
    yield put(orderLectureFailure(error));
  }
}
function* watchOrderLecture() {
  yield takeLatest(ORDER_LECTURE_REQUEST, orderLecture);
}

// Delete
function* deleteLecture({ payload: { id, currentSection, history } }) {
  try {
    yield call(DeleteLecture, id);
    yield put(deleteLectureSuccess());
    const courseId = localStorage.getItem("courseId");
    yield put(getCourseRequest(courseId));
    yield put(setCurrentSection(currentSection));
    // neu con lecture redirect lecture cuoi cung
    const url = redirectLecture({
      lectures: currentSection.lectures,
      courseId,
      lectureId: id,
      sectionId: currentSection.id
    });
    history.push(url);
  } catch (error) {
    yield put(deleteLectureFailure(error));
  }
}

const redirectLecture = ({ lectures, courseId, lectureId, sectionId }) => {
  let url;
  const haveLecture = lectures && lectures.length > 1;
  if (haveLecture) {
    const { array } = removeItemById(lectures, lectureId);
    const lecture = array[array.length - 1];
    url = `/instructor/edit/${courseId}/content/lecture/${lecture.id}`;
  } else {
    url = `/instructor/edit/${courseId}/content/section/${sectionId}`;
  }
  return url;
};

function* watchDeleteLecture() {
  yield takeLatest(DELETE_LECTURE_REQUEST, deleteLecture);
}

export default [
  watchSetCurrentLecture(),
  watchCreateLecture(),
  watchUpdateLecture(),
  watchGetLecture(),
  watchOrderLecture(),
  watchDeleteLecture()
];
