import React from "react";
import Label from "../Label";
import classNames from "classnames";
import style from "./style.scss";

export default ({
  label,
  name,
  originName,
  handleChange,
  index,
  value,
  isShowLabel = true,
  isShowDefaultValue = true,
  invalidText = "Required",
  validText = "",
  isHideInvalidText = false,
  type = "text",
  isViewIcon = true,
  minLength,
  maxLength,
  className,
  ...props
}) => {
  return (
    <div className="form-group">
      {isShowLabel && <Label data={label} required />}
      <input
        value={value || ""}
        type={type}
        className={classNames("form-control", !isViewIcon && style.hideBgImage, className)}
        onChange={e => handleChange(e, originName, index)}
        name={name}
        minLength={minLength}
        maxLength={maxLength}
        required={props["required"] && !isHideInvalidText}
        disabled={props["disabled"]}
        placeholder={props["placeholder"]}
        autoFocus={props["autoFocus"]}
      />
      {!isHideInvalidText && (
        <div className="invalid-feedback">{invalidText}</div>
      )}
      <div className="valid-feedback">{validText}</div>
      {props["isNaN"] && (
        <div className="invalid-feedback d-block">Please enter a number</div>
      )}
    </div>
  );
};
