import React, { Component, Fragment } from "react";
import Footer from "components/WithMentor/Footer";
import { withRouter } from "react-router-dom";
import SideBar from "./components/SideBar";
import SideBarResponsive from "./components/SideBar/Responsive";
import screenSize from "util/ScreenSize";
import withSizes from "react-sizes";
import { mergeArray, getSearchParams } from "util/Common";
import { connect } from "react-redux";
import { getCoursesByCategoryRequest } from "store/Course/action";
import Loading from "components/Loading";
import Pagination from "components/Pagination";
import _ from "lodash";
import { SearchInput } from "components/Field";
import List from "./List";

const allCate = {
  slug: "",
  id: 0,
  name: "All categories"
};
const defaultSize = 6;

class Index extends Component {
  state = {
    search: ""
  };
  componentDidMount() {
    const params = getSearchParams(this.props);
    const page = params.get("page");
    const {
      match: {
        params: { cate }
      }
    } = this.props;
    this.getCourses({ page, cate });
  }

  getCourses = ({ page, cate }) => {
    const { getCoursesByCategoryRequest } = this.props;
    if (!cate) {
      getCoursesByCategoryRequest({
        id: 0,
        params: {
          page: page - 1 || 0,
          size: defaultSize
        },
        withSlug: false
      });
    } else {
      getCoursesByCategoryRequest({
        slug: cate,
        params: {
          page: page - 1 || 0,
          size: defaultSize
        },
        withSlug: true
      });
    }
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  getTextCategory = () => {
    const {
      match: {
        params: { cate }
      },
      listCates
    } = this.props;
    if (cate && listCates) {
      const category = _.find(listCates, e => e.slug === cate);
      return category.name;
    }
    return "";
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    const nextParams = getSearchParams(nextProps);
    const params = getSearchParams(this.props);
    const {
      match: {
        params: { cate }
      }
    } = nextProps;
    const {
      match: {
        params: { cate: oldCate }
      }
    } = this.props;
    const page = nextParams.get("page");
    if (page !== params.get("page") || oldCate !== cate) {
      this.getCourses({ page, cate });
    }
  }

  handleSearch = () => {
    console.log(this.state.search);
  };

  render() {
    const {
      isDesktop,
      width,
      listCates,
      listCourses,
      isLoadingCourse,
      match: { url }
    } = this.props;
    const dataSideBar = mergeArray(allCate, ...listCates);
    const isShow = isLoadingCourse || listCourses.length === 0;
    if (isShow) {
      return <Loading />;
    }
    const coursesData = listCourses.content;
    return (
      <Fragment>
        <div className="text-center z-depth-1 position-relative pt-3">
          <div className="col-md-8 col-12 offset-md-2 col-xl-6 offset-xl-3">
            <SearchInput
              name="search"
              value={this.state.search}
              handleChange={this.handleChange}
              handleSearch={this.handleSearch}
            />
          </div>
        </div>
        <div className="container">
          <h2 className="text-center font-weight-normal h2-responsive pt-4">
            All courses{" "}
            {this.getTextCategory() && (
              <span>
                in{" "}
                <span className="font-weight-bold">
                  {this.getTextCategory()}
                </span>
              </span>
            )}
          </h2>
          <div className="row my-5">
            <div className="col-md-12 col-lg-4 col-xl-3">
              {isDesktop || width === 1024 ? (
                <SideBar data={dataSideBar} />
              ) : (
                <SideBarResponsive data={dataSideBar} />
              )}
            </div>

            <div className="col-md-12 col-lg-8 col-xl-9 align-self-center">
              <List data={coursesData} url={url} />
              <div className="row justify-content-center mt-3">
                <Pagination
                  total={listCourses.totalPages}
                  current={listCourses.page + 1}
                  canPrev={listCourses.page !== 0}
                  canNext={!listCourses.last}
                />
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </Fragment>
    );
  }
}
const mapSizesToProps = ({ width }) => screenSize(width);

const connectIndex = connect(
  ({
    category: { list: listCates },
    course: { isLoading: isLoadingCourse, list: listCourses }
  }) => ({
    listCates,
    isLoadingCourse,
    listCourses
  }),
  { getCoursesByCategoryRequest }
)(Index);

const withSizesIndex = withSizes(mapSizesToProps)(connectIndex);
export default withRouter(withSizesIndex);
