import React, { Component } from "react";
import style from "./style.scss";
import Curriculum from "../Curriculum";
import { withRouter } from "react-router-dom";
import BtnAdd from "pages/Instructor/components/Button/Add";
import { connect } from "react-redux";
import { setCurrentSection } from "store/Instructor/action/section";
import { setCurrentLecture } from "store/Instructor/action/lecture";

class Index extends Component {
  handleAddLecture = () => {
    const { currentSection, currentCourse, setCurrentSection } = this.props;
    if (currentCourse && !currentSection) {
      const section = currentCourse.sections[currentCourse.sections.length - 1];
      setCurrentSection(section);
    }
  };

  render() {
    const {
      match: { url },
      setCurrentLecture,
      setCurrentSection,
      currentSection,
      currentLecture
    } = this.props;
    return (
      <div className={style.contentSideBar}>
        <div>
          <Curriculum />
        </div>

        <div className={style.divButton}>
          <div className="container m-0 p-0">
            <div className="row w-100 m-0 justify-content-between ">
              <div className="col-6 m-0 p-0">
                <BtnAdd
                  onClick={() => {
                    currentSection && setCurrentSection(null);
                    currentLecture && setCurrentLecture(null);
                  }}
                  classCss="w-100 m-0 py-3 px-0"
                  url={`${url}/create/section`}
                  label="Add section"
                />
              </div>
              <div className="col-6 p-0">
                <BtnAdd
                  onClick={() => {
                    currentLecture && setCurrentLecture(null);
                    this.handleAddLecture();
                  }}
                  classCss="w-100 m-0 py-3 px-0"
                  url={`${url}/create/lecture`}
                  label="Add lecture"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse, currentSection, currentLecture } }) => ({
    currentCourse,
    currentSection,
    currentLecture
  }),
  { setCurrentSection, setCurrentLecture }
)(Index);

export default withRouter(connectIndex);
