import React, { Fragment } from "react";
import Label from "./Label";
import Input from "./Input";
import _ from "lodash";

export default ({
  label,
  values,
  desc,
  name,
  handleChange,
  checkAtLeast,
  invalidText = "The requirements must have at least 1 items.",
  length = 5
}) => (
  <Fragment>
    <Label data={label} required />
    <p style={{ height: "50px" }}>{desc}</p>
    {_.times(length, i => {
      return (
        <Input
          isViewIcon={false}
          key={i}
          value={values[i]}
          index={i}
          invalidText={invalidText}
          required={i === length - 1 && !checkAtLeast ? true : false}
          isShowLabel={false}
          handleChange={handleChange}
          originName={name}
          name={`${name}[]`}
          isHideInvalideText={checkAtLeast}
        />
      );
    })}
  </Fragment>
);
