import React, { PureComponent } from "react";
import style from "./style.scss";
import classNames from "classnames";

class Index extends PureComponent {
  state = {
    isHover: false
  };

  onHover = () => {
    this.setState(preState => ({
      isHover: !preState.isHover
    }));
  };

  render() {
    const { children, contentTooltip, classTooltip, isActive } = this.props;
    const { isHover } = this.state;
    return (
      <div className={classTooltip}>
        <div className="position-relative">
          <div
            className={classNames(style.children, isActive && style.active)}
            onMouseOver={this.onHover}
            onMouseOut={this.onHover}
          >
            {children}
          </div>
          {isHover && (
            <div className={style.toolTip}>
              <div className={style.triangleLeft} />
              <div className="px-3 py-2 text-primary font-weight-bold">
                {contentTooltip}
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Index;
