import React from "react";
import { Button } from "mdbreact";

export default ({ course, lectureId, history }) => (
  <div className="py-3 py-md-5 bg-top text-white">
    <div className="container">
      <div className="row">
        <div className="col-xl-5 pl-xl-0">
          <img className="w-100" src={course.coverUrl} alt="overview" />
        </div>
        <div className="col-xl-7 mt-3 mt-xl-0 text-center text-xl-left">
          <h3 className="h3-responsive font-weight-bold">{course.title}</h3>
          <h6 className="h6-responsive py-2" style={{ lineHeight: 2 }}>
            {course.desc}
          </h6>
          <Button
            onClick={() => {
              history.push(`/course/${course.id}/lecture/${lectureId}`);
            }}
            className="ml-0 font-weight-bold "
            color="danger"
          >
            Continue to learn
          </Button>
        </div>
      </div>
    </div>
  </div>
);
