import React, { Component } from "react";
import Title from "pages/Course/components/Title";
import { RadioInput, NumberInput } from "components/Field";
import data from "./data";
import { Button } from "mdbreact";
import { withRouter } from "react-router-dom";
import { updateCoursePriceRequest } from "store/Instructor/action/course";
import { connect } from "react-redux";

// FREE("1"), PAYING("2");
// VIDEO("1"), DOCUMENT("2"), DOWNLOAD("3");

class Index extends Component {
  state = {
    price: 0,
    type: 1
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    const { price, type } = this.state;
    const newPrice = type === 1 ? 0 : price;
    const { updateCoursePriceRequest } = this.props;
    const courseId = localStorage.getItem("courseId");
    const params = { price: newPrice, type };

    updateCoursePriceRequest(courseId, params);
    this.setState(params);
  };

  onClick = value => {
    this.setState({ type: value });
  };

  componentDidMount() {
    const { currentCourse } = this.props;
    if (currentCourse) {
      this.setState(currentCourse);
    }
  }

  render() {
    const { price, type } = this.state;
    return (
      <form
        className="needs-validation"
        noValidate
        onSubmit={this.handleSubmit}
      >
        <div className="container ">
          <div className="bg-white px-5 py-3 mb-3">
            {/** basic info */}
            <Title data="Course Pricing" />
            <div className="container">
              <RadioInput
                data={data}
                name="type"
                onClick={this.onClick}
                current={type}
              />

              <NumberInput
                isShowLabel={false}
                name="price"
                handleChange={this.handleChange}
                value={price}
                isHideInvalidText={type}
                disabled={type === 1}
                required
              />
            </div>
            <div className="text-center">
              <Button type="submit">Save</Button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  undefined,
  { updateCoursePriceRequest }
)(Index);

export default withRouter(connectIndex);
