import React, { Component, Fragment } from "react";
import withJob from "components/WithJob";
import { withRouter } from "react-router-dom";
import { Route } from "react-router-dom";
import PostJob from "./postJob";
import Home from "./homeJob";
import BrowserJob from "./browserJob";
import PrivateRoute from "util/PrivateRoute";

class Index extends Component {
  render() {
    const { match } = this.props;
    return (
      <Fragment>
        <Route exact path={`${match.path}/search`} component={BrowserJob} />
        <PrivateRoute path={`${match.path}/new`} component={PostJob} />
        <Route exact path={match.path} component={Home} />
      </Fragment>
    );
  }
}

export default withJob(withRouter(Index));
