import { fetch } from "util/Api";
import {
  LOGIN_URI,
  REGISTER_URI,
  COURSE_URI,
  SECTION_URI,
  LECTURE_URI,
  VIDEO_URI,
  SKILL_URI,
  USER_URI,
  CATEGORY_URI
} from "./Constants";

export const Login = data => fetch(LOGIN_URI, { data });

export const Register = data => fetch(REGISTER_URI, { data });

// user
export const GetCoursesUser = params =>
  fetch(USER_URI + `/author/course`, { method: "get", params });

export const GetCoursesUserJoin = params =>
  fetch(USER_URI + `/course/join`, { method: "get", params });

export const EnrollCourse = (id, data = {}) =>
  fetch(USER_URI + `/course/${id}/join`, { data });

// course
export const CreateCourse = data => fetch(COURSE_URI, { data });
export const GetCourse = id => fetch(COURSE_URI + `/${id}`, { method: "get" });
export const UpdateCourse = (courseId, data) =>
  fetch(COURSE_URI + `/${courseId}`, { data });

export const UpdateCoursePrice = (courseId, data) =>
  fetch(COURSE_URI + `/${courseId}/update-price`, { data });

export const GetCoursesByCategory = (id, params) =>
  fetch(COURSE_URI + `/findwithcategory/${id}`, {
    params,
    method: "get"
  });

export const GetCoursesByCategorySlug = (slug, params) =>
  fetch(COURSE_URI + `/findwithcategoryslug/${slug}`, {
    params,
    method: "get"
  });

export const FinishLecture = (id, data) =>
  fetch(COURSE_URI + `/${id}/join`, { data });

//section
export const CreateSection = (courseId, data) =>
  fetch(COURSE_URI + `/${courseId}/section`, { data });

export const UpdateSection = (sectionId, data) =>
  fetch(SECTION_URI + `/${sectionId}`, data);

export const OrderSection = data => fetch(SECTION_URI + `/order`, { data });

export const GetSection = sectionId =>
  fetch(SECTION_URI + `/${sectionId}`, { method: "get" });

export const DeleteSection = sectionId =>
  fetch(SECTION_URI + `/${sectionId}`, { method: "delete" });

//lecture
export const CreateLecture = (sectionId, data) =>
  fetch(SECTION_URI + `/${sectionId}/lecture`, { data });

export const UpdateLecture = (lectureId, data) =>
  fetch(LECTURE_URI + `/${lectureId}`, { data });

export const GetLecture = lectureId =>
  fetch(LECTURE_URI + `/${lectureId}`, { method: "get" });

export const OrderLecture = data => fetch(LECTURE_URI + `/order`, { data });

export const DeleteLecture = lectureId =>
  fetch(LECTURE_URI + `/${lectureId}`, { method: "delete" });

// video
export const CreateVideo = data => fetch(VIDEO_URI, { data });

// skill
export const GetListSkills = () => fetch(SKILL_URI, { method: "get" });

// cate
export const GetListCategories = () => fetch(CATEGORY_URI, { method: "get" });
