import React from "react";
import classNames from "classnames";
import Tooltip from "../Tooltip";
import { Link, withRouter } from "react-router-dom";
import style from "./style.scss";
import { checkCurrentUrl } from "util/Common";
import getItemUrl from "./getItemUrl";

const Index = ({
  data,
  match: {
    url,
    params: { id }
  },
  lectureId,
  urlBack,
  location: { state }
}) => {
  return (
    <div className={classNames(style.sideBar, "text-center")}>
      {data.map((item, i) => {
        let itemUrl = getItemUrl({ i, id, lectureId, urlBack, item });
        return (
          <Link
            key={i}
            to={{
              pathname: itemUrl,
              state: {
                ...state
              }
            }}
          >
            <Tooltip
              contentTooltip={item.tooltip}
              classTooltip="mt-4"
              isActive={checkCurrentUrl(item.url, url)}
            >
              {item.icon}
            </Tooltip>
          </Link>
        );
      })}
    </div>
  );
};

export default withRouter(Index);
