const checkWidth = (width, min, max) => width > min && width < max;

export default width => {
  return {
    width: width,
    isDesktop: width > 1024,
    isExtraLarge: checkWidth(width, 1200, 2500),
    isLarge: checkWidth(width, 992, 1200),
    isMedium: checkWidth(width, 768, 992),
    isSmall: checkWidth(width, 576, 768),
    isExtraSmall: checkWidth(width, 300, 576)
  };
};
