import React, { Component } from "react";
import Form from "./Form";
import BtnNext from "pages/Instructor/components/Button/Next";
import Stepper from "pages/Instructor/components/Stepper";
import { withRouter } from "react-router-dom";
import { getCourseRequest } from "store/Instructor/action/course";
import { getListSkillsRequest } from "store/Skill/action";
import { connect } from "react-redux";
import Loading from "components/Loading";

class Index extends Component {
  componentDidMount() {
    const {
      getCourseRequest,
      currentCourse,
      getListSkillsRequest
    } = this.props;
    if (!currentCourse) {
      const courseId = localStorage.getItem("courseId");
      getCourseRequest(courseId);
    }
    getListSkillsRequest();
  }

  render() {
    const courseId = localStorage.getItem("courseId");
    const { history, currentCourse, listSkills } = this.props;
    if (!currentCourse) {
      return <Loading />;
    }
    return (
      <div>
        <div className="p-5">
          <Stepper currentStep={2} />
        </div>
        <Form currentCourse={currentCourse} listSkills={listSkills} />
        <div className="container">
          <div className="bg-white d-flex align-content-center justify-content-between p-3 mb-3">
            <BtnNext
              label="Back"
              onClick={() => {
                history.push(`/instructor/edit/${courseId}/content`);
              }}
            />
            <BtnNext
              onClick={() => {
                history.push(`/instructor/edit/${courseId}/price`);
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse }, skill: { list: listSkills } }) => ({
    currentCourse,
    listSkills
  }),
  { getCourseRequest, getListSkillsRequest }
)(Index);

export default withRouter(connectIndex);
