export const GET_LIST_CATEGORIES_REQUEST = "GET_LIST_CATEGORIES_REQUEST";
export const GET_LIST_CATEGORIES_SUCCESS = "GET_LIST_CATEGORIES_SUCCESS";
export const GET_LIST_CATEGORIES_FAILURE = "GET_LIST_CATEGORIES_FAILURE";

export const getListCategoriesRequest = () => {
  return {
    type: GET_LIST_CATEGORIES_REQUEST
  };
};

export const getListCategoriesSuccess = data => {
  return {
    type: GET_LIST_CATEGORIES_SUCCESS,
    payload: { data }
  };
};

export const getListCategoriesFailure = error => ({
  type: GET_LIST_CATEGORIES_FAILURE,
  payload: { error }
});
