import React from "react";
import style from "./style.scss";
import classNames from "classnames";

export default ({ name, value, handleChange, handleSearch }) => (
  <div className="input-group py-3">
    <input
      value={value || ""}
      type="text"
      className={classNames("form-control", style.input)}
      placeholder="Search"
      aria-describedby="basic-addon"
      onChange={e => handleChange(e)}
      onKeyDown={e => {
        if (e.keyCode === 13) {
          handleSearch();
        }
      }}
      name={name}
    />
    <div
      className={classNames("input-group-prepend cursor-pointer ")}
      onClick={() => handleSearch()}
    >
      <span
        className={classNames("input-group-text", style.icon)}
        id="basic-addon"
      >
        <i className="fa fa-search suffix" />
      </span>
    </div>
  </div>
);
