import React, { PureComponent, Fragment } from "react";
import Section from "./Section";
import _ from "lodash";

class Index extends PureComponent {
  state = {
    collapse: -1
  };

  toggle = index => {
    this.setState(preState => ({
      collapse: preState.collapse === index ? -1 : index
    }));
  };

  componentDidMount() {
    const { lecture } = this.props;
    if (lecture) {
      this.toggle(lecture.sectionId);
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { lecture } = nextProps;
    const { collapse } = this.state;
    if (lecture) {
      collapse !== lecture.sectionId && this.toggle(lecture.sectionId);
    }
  }

  render() {
    const { collapse } = this.state;
    const { sections, lectureFinishList } = this.props;

    return (
      <Fragment>
        {sections.map((section, index) => {
          const lectures = section.lectures ? section.lectures : [];
          const lectureIds = lectures.map(lecture => lecture.id + "");

          let countLectureFinishSection = 0;
          if (lectureFinishList) {
            countLectureFinishSection =
              lectures.length -
              _.differenceWith(lectureIds, lectureFinishList, _.isEqual).length;
          }
          return (
            <Section
              countLectureFinishSection={countLectureFinishSection}
              lectureFinishList={lectureFinishList}
              key={index}
              toggle={this.toggle}
              length={lectures.length}
              section={section}
              index={section.id}
              collapse={collapse}
            />
          );
        })}
      </Fragment>
    );
  }
}

export default Index;
