import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import { Fa } from "mdbreact";
import style from "./style.scss";
import classnames from "classnames";

export default ({
  navUrl,
  subnav,
  hoverNavbar,
  navIndex,
  isShowNavSub,
  navSubIndex
}) => {
  return (
    <Fragment>
      <div className={classnames(style.navSub, "box-shadow-default")}>
        {subnav &&
          subnav.map((nav, index) => (
            <div
              onMouseEnter={() => {
                hoverNavbar({
                  navIndex: navIndex,
                  isShowNav: true,
                  navSubIndex: index,
                  isShowNavSub: true
                });
              }}
              onMouseLeave={() => {
                hoverNavbar({
                  navIndex: null,
                  isShowNav: false,
                  navSubIndex: null,
                  isShowNavSub: false
                });
              }}
              key={index}
            >
              {/**  */}
              <Link key={index} to={`${navUrl}${nav.url}`}>
                <div
                  className={classnames(
                    "p-3 text-capitalize d-flex justify-content-between text-black",
                    style.navItem,
                    index === subnav.length - 1 && "border-0",
                    isShowNavSub && navSubIndex === index && style.navItemHover2
                  )}
                >
                  <div>{nav.name}</div>
                  {index === navSubIndex && isShowNavSub && nav.subnav && (
                    <div>
                      <Fa icon="caret-right" />
                    </div>
                  )}
                </div>
              </Link>
              {/** java sub sub */}
              {index === navSubIndex && isShowNavSub && nav.subnav && (
                <div
                  className={classnames(
                    "container-fluid p-0 box-shadow-default",
                    style.navSubSub
                  )}
                >
                  <div
                    className={classnames(
                      "text-white font-weight-bold text-center py-3 ",
                      style.navTitle
                    )}
                  >
                    {nav.name}
                  </div>
                  <div className="row mt-3 ml-3">
                    {nav.subnav &&
                      nav.subnav.map((navItem, index) => {
                        const baseUri = navUrl.includes("/experts")
                          ? "/search"
                          : navUrl + nav.url;
                        return (
                          <Link
                            key={index}
                            to={`${baseUri}${navItem.url}`}
                            className={classnames(
                              "text-capitalize text-white col-4 mb-2",
                              style.navLinkUnderline
                            )}
                          >
                            {navItem.name}
                          </Link>
                        );
                      })}
                  </div>
                </div>
              )}
            </div>
          ))}
      </div>
    </Fragment>
  );
};
