import {
  SET_CURRENT_SECTION,
  UPDATE_SECTIONS,
  CREATE_SECTION_FAILURE,
  CREATE_SECTION_REQUEST,
  CREATE_SECTION_SUCCESS,
  UPDATE_SECTION_FAILURE,
  UPDATE_SECTION_REQUEST,
  UPDATE_SECTION_SUCCESS,
  GET_SECTION_FAILURE,
  GET_SECTION_REQUEST,
  GET_SECTION_SUCCESS,
  ORDER_SECTION_FAILURE,
  ORDER_SECTION_REQUEST,
  ORDER_SECTION_SUCCESS,
  DELETE_SECTION_FAILURE,
  DELETE_SECTION_REQUEST,
  DELETE_SECTION_SUCCESS,
  TOGGLE_SECTION
} from "../action/section";

export default (state, action) => {
  switch (action.type) {
    case TOGGLE_SECTION:
      const { currentSection, collapse } = state;
      const checkChangeToggle =
        currentSection &&
        currentSection.id !== action.payload.id &&
        collapse[action.payload.id];
      if (checkChangeToggle) return { newState: state, isNewState: false };
      return {
        newState: {
          ...state,
          collapse: {
            ...state.collapse,
            [action.payload.id]: !state.collapse[action.payload.id]
          }
        },
        isNewState: true
      };

    case SET_CURRENT_SECTION:
      return {
        newState: {
          ...state,
          currentSection: action.payload.section
            ? {
                ...action.payload.section
              }
            : null
        },
        isNewState: true
      };
    case UPDATE_SECTIONS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentCourse: {
            ...state.currentCourse,
            sections: [...state.currentCourse.sections]
          }
        },
        isNewState: true
      };
    // create
    case CREATE_SECTION_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          params: action.payload.params,
          history: action.payload.history
        },
        isNewState: true
      };
    case CREATE_SECTION_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentCourse: {
            ...state.currentCourse,
            sections: [...state.currentCourse.sections, action.payload.section]
          },
          currentSection: {
            ...action.payload.section
          }
        },
        isNewState: true
      };
    // get
    case GET_SECTION_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          id: action.payload.id
        },
        isNewState: true
      };
    case GET_SECTION_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentSection: {
            ...action.payload.section
          }
        },
        isNewState: true
      };
    // order
    case ORDER_SECTION_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case ORDER_SECTION_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false
        },
        isNewState: true
      };

    // update
    case UPDATE_SECTION_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          id: action.payload.id,
          params: action.payload.params,
          history: action.payload.history
        },
        isNewState: true
      };
    case UPDATE_SECTION_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentSection: {
            ...state.currentSection,
            ...action.payload.data
          }
        },
        isNewState: true
      };
    // DELETE
    case DELETE_SECTION_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case DELETE_SECTION_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false
        },
        isNewState: true
      };

    // failure
    case CREATE_SECTION_FAILURE:
    case GET_SECTION_FAILURE:
    case UPDATE_SECTION_FAILURE:
    case ORDER_SECTION_FAILURE:
    case DELETE_SECTION_FAILURE:
      return {
        newState: {
          ...state,
          isLoading: false,
          error: action.payload.error
        },
        isNewState: true
      };
    default:
      return { newState: state, isNewState: false };
  }
};
