import {
  GET_LECTURE_FAILURE,
  GET_LECTURE_REQUEST,
  GET_LECTURE_SUCCESS
} from "./action";

const initialState = {
  current: null,
  isLoading: true,
  error: null
};

export default (state = initialState, action) => {
  switch (action.type) {
    // get by id
    case GET_LECTURE_REQUEST:
      return {
        ...state,
        isLoading: true,
        payload: action.payload
      };
    case GET_LECTURE_SUCCESS:
      return {
        ...state,
        isLoading: false,
        current: action.payload.lecture
      };

    case GET_LECTURE_FAILURE:
      return {
        ...state,
        isLoading: false,
        error: action.payload.error
      };
    default:
      return state;
  }
};
