import React from "react";
import { Button } from "mdbreact";
import { withRouter } from "react-router-dom";
import classNames from "classnames";

const Index = ({
  history,
  url,
  label,
  Icon = () => <span />,
  onClick = () => {},
  isRedirect = true,
  classCss = ""
}) => (
  <Button
    className={classNames("font-weight-bold", classCss)}
    color="primary"
    size="lg"
    style={{ borderRadius: 0 }}
    onClick={() => {
      onClick();
      isRedirect && history.push(url);
    }}
  >
    <span className="align-text-bottom mr-2">
      <Icon />
    </span>
    {label}
  </Button>
);

export default withRouter(Index);
