import React from "react";
import { Link } from "react-router-dom";
import { Button } from "mdbreact";
import { logout } from "store/Auth/action";
import { connect } from "react-redux";
import Dropdown from "./Dropdown";
import { MDBDropdownItem } from "mdbreact";
import {
  FaSignOutAlt,
  FaInfoCircle,
  FaUserGraduate,
  FaBook
} from "react-icons/fa";

const Index = ({ logout }) => (
  <div className="d-flex">
    <Link to={"/instructor/create/course"}>
      <Button color="default">Create course</Button>
    </Link>

    <Dropdown>
      <MDBDropdownItem>
        <Link to="/my-courses">
          <FaUserGraduate size={20} /> <span className="ml-2">My course</span>
        </Link>
      </MDBDropdownItem>
      <MDBDropdownItem>
        <Link to="/profile">
          <FaInfoCircle size={20} /> <span className="ml-2">My profile</span>
        </Link>
      </MDBDropdownItem>
      <MDBDropdownItem>
        <Link to="/instructor">
          <FaBook size={20} /> <span className="ml-2">Instructor area</span>
        </Link>
      </MDBDropdownItem>
      <MDBDropdownItem divider />
      <MDBDropdownItem onClick={logout}>
        <div className="">
          <FaSignOutAlt size={20} /> <span className="ml-2">Logout</span>
        </div>
      </MDBDropdownItem>
    </Dropdown>
  </div>
);

const connectIndex = connect(
  undefined,
  { logout }
)(Index);

export default connectIndex;
