import React, { Fragment, PureComponent } from "react";
import { Fa } from "mdbreact";
import classNames from "classnames";
import { Collapse } from "mdbreact";
import Lecture from "./Lecture";
import _ from "lodash";

class Index extends PureComponent {
  render() {
    const {
      collapse,
      index,
      section,
      length,
      toggle,
      countLectureFinishSection,
      lectureFinishList
    } = this.props;

    const toggleCollapse = collapse === index;
    return (
      <Fragment key={index}>
        <div
          className={classNames(
            "px-4 py-3 d-flex cursor-pointer ease-in-out box-shadow-hover bg-white mt-3"
          )}
          onClick={() => toggle(index)}
        >
          <Fragment>
            <div>
              <Fa
                className="text-primary"
                icon={toggleCollapse ? "folder" : "folder-o"}
              />
            </div>
            <div
              className={
                toggleCollapse
                  ? "mx-2 font-weight-bold text-primary"
                  : "mx-2 font-weight-normal text-info"
              }
            >
              {section.title}
            </div>
            <div
              className={classNames(
                "ml-auto mr-2",
                toggleCollapse && "font-weight-bold text-primary"
              )}
            >
              {countLectureFinishSection}/{length}
            </div>
            <div>
              {toggleCollapse ? (
                <Fa icon={"caret-up"} className="text-primary" />
              ) : (
                <Fa icon={"caret-down"} />
              )}
            </div>
          </Fragment>
        </div>
        <Collapse isOpen={toggleCollapse}>
          {section.lectures.map((lecture, index) => {
            const seen = _.includes(lectureFinishList, lecture.id + "");
            return <Lecture key={index} lecture={lecture} seen={seen} />;
          })}
        </Collapse>
      </Fragment>
    );
  }
}

export default Index;
