import React, { Fragment } from "react";
import { withRouter } from "react-router-dom";
import { Route } from "react-router-dom";
import WithMentor from "components/WithMentor";
import Content from "./Content";
import Overview from "./Overview";
import Review from "./Review";
import List from "./List";
import Enroll from "./Enroll";

const Index = ({ match }) => {
  return (
    <Fragment>
      <Route path={`${match.path}/:id/overview`} component={Overview} />
      <Route path={`${match.path}/:id/review`} component={Review} />
      <Route path={`${match.path}/:id/enroll`} component={Enroll} />
      <Route
        exact
        path={`${match.path}/:id/lecture/:lectureId`}
        component={Content}
      />

      <Route exact path={`${match.path}/category/:cate`} component={List} />
      <Route exact path={`${match.path}`} component={List} />
    </Fragment>
  );
};

export default WithMentor(withRouter(Index), {
  isViewFooter: false
});
