export const SET_CURRENT_LECTURE = "SET_CURRENT_LECTURE";

export const CREATE_LECTURE_REQUEST = "CREATE_LECTURE_REQUEST";
export const CREATE_LECTURE_SUCCESS = "CREATE_LECTURE_SUCCESS";
export const CREATE_LECTURE_FAILURE = "CREATE_LECTURE_FAILURE";

export const GET_LECTURE_REQUEST = "GET_LECTURE_REQUEST";
export const GET_LECTURE_SUCCESS = "GET_LECTURE_SUCCESS";
export const GET_LECTURE_FAILURE = "GET_LECTURE_FAILURE";

export const UPDATE_LECTURE_REQUEST = "UPDATE_LECTURE_REQUEST";
export const UPDATE_LECTURE_SUCCESS = "UPDATE_LECTURE_SUCCESS";
export const UPDATE_LECTURE_FAILURE = "UPDATE_LECTURE_FAILURE";

export const ORDER_LECTURE_REQUEST = "ORDER_LECTURE_REQUEST";
export const ORDER_LECTURE_SUCCESS = "ORDER_LECTURE_SUCCESS";
export const ORDER_LECTURE_FAILURE = "ORDER_LECTURE_FAILURE";

export const DELETE_LECTURE_REQUEST = "DELETE_LECTURE_REQUEST";
export const DELETE_LECTURE_SUCCESS = "DELETE_LECTURE_SUCCESS";
export const DELETE_LECTURE_FAILURE = "DELETE_LECTURE_FAILURE";

export const setCurrentLecture = lecture => ({
  type: SET_CURRENT_LECTURE,
  payload: { lecture }
});

//create
export const createLectureRequest = (sectionId, params, history) => ({
  type: CREATE_LECTURE_REQUEST,
  payload: { sectionId, params, history }
});

export const createLectureSuccess = lecture => {
  return {
    type: CREATE_LECTURE_SUCCESS,
    payload: { lecture }
  };
};

export const createLectureFailure = error => ({
  type: CREATE_LECTURE_FAILURE,
  payload: { error }
});

//get by id
export const getLectureRequest = payload => ({
  type: GET_LECTURE_REQUEST,
  payload
});

export const getLectureSuccess = lecture => {
  return {
    type: GET_LECTURE_SUCCESS,
    payload: { lecture }
  };
};

export const getLectureFailure = error => ({
  type: GET_LECTURE_FAILURE,
  payload: { error }
});

// update by id
export const updateLectureRequest = payload => ({
  type: UPDATE_LECTURE_REQUEST,
  payload
});

export const updateLectureSuccess = data => {
  return {
    type: UPDATE_LECTURE_SUCCESS,
    payload: { data }
  };
};

export const updateLectureFailure = error => ({
  type: UPDATE_LECTURE_FAILURE,
  payload: { error }
});

// order
export const orderLectureRequest = payload => ({
  type: ORDER_LECTURE_REQUEST,
  payload
});

export const orderLectureSuccess = () => {
  return {
    type: ORDER_LECTURE_SUCCESS
  };
};

export const orderLectureFailure = error => ({
  type: ORDER_LECTURE_FAILURE,
  payload: { error }
});

// delete
export const deleteLectureRequest = payload => ({
  type: DELETE_LECTURE_REQUEST,
  payload
});

export const deleteLectureSuccess = () => {
  return {
    type: DELETE_LECTURE_SUCCESS
  };
};

export const deleteLectureFailure = error => ({
  type: DELETE_LECTURE_FAILURE,
  payload: { error }
});
