import React, { PureComponent } from "react";
import style from "./style.scss";
import {
  FaRegCircle,
  FaCheckCircle,
  FaRegFileAlt,
  FaRegPlayCircle
} from "react-icons/fa";
import { withRouter } from "react-router-dom";
import classNames from "classnames";
import { connect } from "react-redux";
import { TYPE_LECTURE } from "util/Constants";

class Lecture extends PureComponent {
  render() {
    const { currentLecture, lecture, course, seen, history } = this.props;

    // eslint-disable-next-line
    const active = currentLecture && currentLecture.id == lecture.id;
    // eslint-disable-next-line
    const checkNextLecture = course.lecture.nextId == lecture.id;

    const canClick = seen || active || checkNextLecture;
    return (
      <div key={lecture.id} className={classNames("border-top")}>
        <div
          onClick={() => {
            if (canClick) {
              history.push(`/course/${course.id}/lecture/${lecture.id}`);
            }
          }}
          className={classNames(
            "px-4 py-3 pl-5 d-flex bg-white align-items-center justify-content-between font-weight-normal",
            style.default,
            !canClick && style.inactive,
            active && style.active
          )}
        >
          <div>
            <span className="">
              {lecture.type === TYPE_LECTURE["DOCUMENT"] ? (
                <FaRegFileAlt />
              ) : (
                <FaRegPlayCircle />
              )}
            </span>
            <span className="ml-2">{lecture.title}</span>
          </div>

          {/** check seen */}
          <div className={"ml-2"}>
            {seen ? (
              <FaCheckCircle className="text-success" />
            ) : (
              <FaRegCircle />
            )}
          </div>
        </div>
      </div>
    );
  }
}

const connectIndex = connect(
  ({ course: { current: course }, lecture: { current: currentLecture } }) => ({
    course,
    currentLecture
  }),
  undefined
)(Lecture);

export default withRouter(connectIndex);
