export const GET_COURSE_REQUEST = "GET_COURSE_REQUEST";
export const GET_COURSE_SUCCESS = "GET_COURSE_SUCCESS";
export const GET_COURSE_FAILURE = "GET_COURSE_FAILURE";

export const FINISH_LECTURE_REQUEST = "FINISH_LECTURE_REQUEST";
export const FINISH_LECTURE_SUCCESS = "FINISH_LECTURE_SUCCESS";
export const FINISH_LECTURE_FAILURE = "FINISH_LECTURE_FAILURE";

export const ENROLL_COURSE_REQUEST = "ENROLL_COURSE_REQUEST";
export const ENROLL_COURSE_SUCCESS = "ENROLL_COURSE_SUCCESS";
export const ENROLL_COURSE_FAILURE = "ENROLL_COURSE_FAILURE";

export const GET_COURSES_JOIN_REQUEST = "GET_COURSES_JOIN_REQUEST";
export const GET_COURSES_JOIN_SUCCESS = "GET_COURSES_JOIN_SUCCESS";
export const GET_COURSES_JOIN_FAILURE = "GET_COURSES_JOIN_FAILURE";

// get by category
export const GET_COURSES_BY_CATEGORY_REQUEST =
  "GET_COURSES_BY_CATEGORY_REQUEST";
export const GET_COURSES_BY_CATEGORY_SUCCESS =
  "GET_COURSES_BY_CATEGORY_SUCCESS";
export const GET_COURSES_BY_CATEGORY_FAILURE =
  "GET_COURSES_BY_CATEGORY_FAILURE";

//get by id
export const getCourseRequest = id => ({
  type: GET_COURSE_REQUEST,
  payload: { id }
});

export const getCourseSuccess = course => {
  localStorage.setItem("courseId", course.id);
  return {
    type: GET_COURSE_SUCCESS,
    payload: { course }
  };
};

export const getCourseFailure = error => ({
  type: GET_COURSE_FAILURE,
  payload: { error }
});

//enroll course
export const enrollCourseRequest = payload => ({
  type: ENROLL_COURSE_REQUEST,
  payload
});

export const enrollCourseSuccess = () => {
  return {
    type: ENROLL_COURSE_SUCCESS
  };
};

export const enrollCourseFailure = error => ({
  type: ENROLL_COURSE_FAILURE,
  payload: { error }
});

// finish lecture
export const finishLectureRequest = payload => ({
  type: FINISH_LECTURE_REQUEST,
  payload
});

export const finishLectureSuccess = id => {
  return {
    type: FINISH_LECTURE_SUCCESS,
    payload: { id }
  };
};

export const finishLectureFailure = error => ({
  type: FINISH_LECTURE_FAILURE,
  payload: { error }
});

//get by category
export const getCoursesByCategoryRequest = payload => ({
  type: GET_COURSES_BY_CATEGORY_REQUEST,
  payload
});

export const getCoursesByCategorySuccess = courses => {
  return {
    type: GET_COURSES_BY_CATEGORY_SUCCESS,
    payload: { courses }
  };
};

export const getCoursesByCategoryFailure = error => ({
  type: GET_COURSES_BY_CATEGORY_FAILURE,
  payload: { error }
});

//get courses user joined
export const getCoursesJoinRequest = () => ({
  type: GET_COURSES_JOIN_REQUEST
});

export const getCoursesJoinSuccess = courses => {
  return {
    type: GET_COURSES_JOIN_SUCCESS,
    payload: { courses }
  };
};

export const getCoursesJoinFailure = error => ({
  type: GET_COURSES_JOIN_FAILURE,
  payload: { error }
});
