import React, { PureComponent, Fragment } from "react";
import { Input, Label, CKEditor } from "components/Field";
import Title from "pages/Course/components/Title";
import { Button } from "mdbreact";
import { FaPlayCircle, FaRegFileAlt } from "react-icons/fa";
import { withRouter } from "react-router-dom";
import _ from "lodash";
import Dropzone from "pages/Instructor/components/Dropzone";
import { connect } from "react-redux";
import {
  setCurrentLecture,
  createLectureRequest,
  updateLectureRequest
} from "store/Instructor/action/lecture";

const defaultType = {
  VIDEO: 1,
  DOCUMENT: 2
};

const defaultState = {
  title: "",
  type: defaultType["VIDEO"],
  content: "",
  isFirstChange: true,
  newVideo: null,
  files: []
};

class Index extends PureComponent {
  state = defaultState;

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  handleChangeEditor = content => {
    this.setState({ content, isFirstChange: false });
  };

  chooseType = type => {
    this.setState({ type });
  };

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    this.setState({ isFirstChange: false });
    const {
      isCreate,
      currentSection,
      currentLecture,
      currentCourse,
      history,
      createLectureRequest,
      updateLectureRequest
    } = this.props;
    let params = _.omit(this.state, "isFirstChange");

    if (isCreate) {
      const section = !currentSection
        ? currentCourse.sections[currentCourse.sections.length - 1]
        : currentSection;

      const { type, content, newVideo } = this.state;
      if (
        (type === defaultType["DOCUMENT"] && content) ||
        (type === defaultType["VIDEO"] && newVideo)
      ) {
        if (section) {
          createLectureRequest(section.id, params, history);
        }
      }
    } else {
      // update lecture
      updateLectureRequest({
        id: currentLecture.id,
        params
      });
    }
  };

  onDrop = files => this.setState({ files, newVideo: files[0] });
  onCancel = () => this.setState({ files: [], newVideo: null });

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { currentLecture } = nextProps;
    if (currentLecture) {
      this.setState({
        ...nextProps.currentLecture,
        files: currentLecture.video ? [currentLecture.video] : []
      });
    }
  }

  componentDidMount() {
    const { currentCourse, history } = this.props;
    if (currentCourse && currentCourse.sections.length === 0) {
      history.push(
        `/instructor/edit/${currentCourse.id}/content/create/section`
      );
    }
  }

  render() {
    const { title, type, content, isFirstChange, files } = this.state;
    const { label } = this.props;
    return (
      <form
        className="needs-validation"
        noValidate
        onSubmit={this.handleSubmit}
      >
        <div className="bg-white p-3 mb-3">
          {/** basic info */}
          <Title data={label} />
          <div className="container">
            <Input
              label="Lecture title"
              name="title"
              handleChange={this.handleChange}
              value={title}
              required
              autoFocus
            />

            <Label data="Select main type of content" />
            <div className="mb-3">
              <Button
                onClick={() => this.chooseType(defaultType["VIDEO"])}
                color={
                  type === defaultType["VIDEO"]
                    ? "light-blue darken-4"
                    : "white"
                }
              >
                <span className="align-text-bottom mr-1">
                  <FaPlayCircle />
                </span>
                Video
              </Button>
              <Button
                onClick={() => this.chooseType(defaultType["DOCUMENT"])}
                color={
                  type === defaultType["DOCUMENT"]
                    ? "light-blue darken-4"
                    : "white"
                }
              >
                <span className="align-text-bottom mr-1">
                  <FaRegFileAlt />
                </span>
                Article
              </Button>
            </div>

            {/** article */}
            {type === defaultType["DOCUMENT"] && (
              <Fragment>
                <Label data="Article" required />
                <CKEditor
                  activeClass="content_lecture_detail"
                  content={content}
                  isFirstChange={isFirstChange}
                  handleChange={this.handleChangeEditor}
                />
              </Fragment>
            )}

            {/** video */}
            {type === defaultType["VIDEO"] && (
              <Fragment>
                <Label data="Choose video" required />
                <Dropzone
                  accept="video/mp4,video/x-m4v,video/*"
                  onDrop={this.onDrop}
                  onCancel={this.onCancel}
                  multiple={false}
                  files={files}
                  type="video"
                />
              </Fragment>
            )}

            <div className="text-center mt-2">
              <Button type="submit">Save</Button>
            </div>

            {/** end basic info */}
          </div>
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse, currentSection, currentLecture } }) => ({
    currentSection,
    currentLecture,
    currentCourse
  }),
  { setCurrentLecture, createLectureRequest, updateLectureRequest }
)(Index);

export default withRouter(connectIndex);
