import React from "react";
import { Card, CardBody } from "mdbreact";
import { getSomeWordsFromString } from "util/Common";
import { FaBars } from "react-icons/fa";
import style from "./style.scss";
import classNames from "classnames";
import Rating from "react-rating";
import Image from "components/Image";

export default ({ data, isHover = true }) => (
  <div className={isHover ? "box-shadow-hover scale-hover-05 " : ""}>
    <Card className="cursor-pointer">
      <Image src={data.coverUrl} />
      <CardBody>
        <h6 className="d-flex justify-content-between text-black-50 align-items-center h6-responsive  ">
          <div>{data.userSummary ? data.userSummary.fullName : "Hidden"}</div>
          <div className="font-size-13">
            <span className={style.iconLecture}>
              <FaBars />
            </span>
            <span className="ml-1">{data.numberOfLectures || 0} lectures</span>
          </div>
        </h6>
        <h6
          className={classNames(
            style.title,
            "font-weight-bold mt-2 h6-responsive"
          )}
        >
          {getSomeWordsFromString(data.title, 15)}
        </h6>
        <div className={classNames("text-black-70 font-size-13", style.desc)}>
          {getSomeWordsFromString(data.description)}
        </div>
        <div>
          <Rating
            emptySymbol="fa fa-star-o "
            fullSymbol="fa fa-star text-primary"
            readonly
            initialRating={data.rating || 5}
          />
          <span className="mx-2">{data.rating}</span>
          <span>({data.numberOfUsers || 0})</span>
        </div>

        {data.type === 1 ? (
          <div className="text-right font-weight-bold">Free</div>
        ) : (
          <div className="text-right font-weight-bold">${data.price}</div>
        )}
      </CardBody>
    </Card>
  </div>
);
