import React from 'react';
import classnames from "classnames";
import {Link} from "react-router-dom";
import style from './style.scss';
import {Fa} from 'mdbreact';

const socialButton = ['facebook-f', 'youtube-play'];

const footerLink = [
    {
        category: 'Talent',
        subCategory: [
            {
                to: '/how-it-work',
                title: 'How it work'
            },
            {
                to: '/how-it-work',
                title: 'How it work'
            }
        ]
    },
    {
        category: 'Talent',
        subCategory: [
            {
                to: '/how-it-work',
                title: 'How it work'
            },
            {
                to: '/how-it-work',
                title: 'How it work'
            }
        ]
    }, {
        category: 'Talent',
        subCategory: [
            {
                to: '/how-it-work',
                title: 'How it work'
            },
            {
                to: '/how-it-work',
                title: 'How it work'
            }
        ]
    },
];

class Footer extends React.Component {
    render() {

        return (
            <div className={'width py-5'} style={{background: '#324258'}}>
                <div className='container'>
                    <div className='row'>
                        <div className={'d-flex col-12 col-sm-3 mb-3'}>
                            <img className={classnames(style.logo)}
                                 src={require('images/common/network.png')}
                                 alt="LOGO Hello work"/>
                            <div className='ml-2 logo-title' style={{color: '#fafafa'}}>Hello work
                            </div>
                        </div>
                        {footerLink.map((ele, index) => (
                            <div key={index} className={'text-white col-6 col-sm-2'}>
                                <h5 className={'text-white'} style={{fontWeight: '400'}}>
                                    Talent
                                </h5>
                                <ul className={style.footerUl}>
                                    {ele.subCategory.map((ele, index) => {
                                        return (
                                            <li className={style.footerLi} key={index}>
                                                <Link to={ele.to}>{ele.title}</Link>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                        ))}
                        <div className={'col-3'}>
                            <h5 className={'text-white'} style={{fontWeight: '400'}}>
                                Social
                            </h5>
                            <div className={'d-flex'}>
                                {socialButton.map((ele, index) => (
                                    <Link key={index} to={'/asdad'}>
                                        <Fa key={index} size='lg' className={style.footerLi + ' mr-4 ease-in-out'}
                                            icon={ele}/>
                                    </Link>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Footer