import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
  REGISTER_FAILURE,
  LOGOUT
} from "./action";
import { getToken } from "util/Common";

const initialState = {
  data: null,
  token: null,
  error: null,
  isAuth: !!getToken(),
  isLoading: false,
  isRegister: false
};

const getError = error => ({
  message: error.data.message,
  status: error.status
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_REQUEST:
      return {
        ...state,
        payload: action.payload,
        isLoading: true
      };
    case LOGIN_SUCCESS:
      return {
        ...state,
        isAuth: true
      };

    case REGISTER_REQUEST:
      return {
        ...state,
        data: action.payload.data,
        isLoading: true
      };
    case REGISTER_SUCCESS:
      return {
        ...state,
        isRegister: true
      };
    case REGISTER_FAILURE:
    case LOGIN_FAILURE:
      return {
        ...state,
        error: getError(action.payload.error)
      };
    case LOGOUT:
      return {
        ...state,
        isAuth: false
      };
    default:
      return state;
  }
};

export default reducer;
