import React, { PureComponent } from "react";
import { CKEditor, Select, MultiInput, Label } from "components/Field";
import data from "./data";
import Title from "pages/Course/components/Title";
import defaultState from "./defaultState";
import { checkAtLeast, isDataNull, getListData } from "util/Common";
import { withRouter } from "react-router-dom";
import { Button } from "mdbreact";
import { updateCourseRequest } from "store/Instructor/action/course";
import { connect } from "react-redux";
import Dropzone from "pages/Instructor/components/Dropzone";
import { LEVEL } from "util/Constants";
import _ from "lodash";

class Index extends PureComponent {
  state = defaultState;

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    this.setState({
      isFirstChange: {
        description: false,
        skill: false,
        level: false
      }
    });

    const { currentCourse, updateCourseRequest } = this.props;
    const checkDataNull = isDataNull(this.state, [
      "description",
      "skills",
      "level"
    ]);
    const {
      description,
      level,
      skills,
      requirement,
      target,
      image
    } = this.state;

    if (!checkDataNull && checkAtLeast(requirement) && checkAtLeast(target)) {
      const skillId = _.map(skills, "value");

      const courseId = localStorage.getItem("courseId");
      const params = {
        ...currentCourse,
        description,
        level: level.value,
        requirement,
        skillId,
        target,
        image
      };
      updateCourseRequest(courseId, params);
    }
  };

  handleChangeMulti = ({ target: { value } }, originName, index) => {
    let newData = this.state[originName];
    newData[index] = value;
    this.setState({
      [originName]: [...newData]
    });
  };

  handleChangeSelect = name => value => {
    this.setState({
      [name]: value,
      isFirstChange: {
        ...this.state.isFirstChange,
        [name]: false
      }
    });
  };

  handleChangeEditor = description => {
    this.setState({
      description: description,
      isFirstChange: {
        ...this.state.isFirstChange,
        description: false
      }
    });
  };

  onDrop = files => {
    this.setState({
      files,
      image: files[0]
    });
  };
  onCancel = () => this.setState({ files: [], image: null });

  componentDidMount() {
    const { currentCourse } = this.props;

    if (currentCourse) {
      const { skills } = currentCourse;
      this.setState({
        ...currentCourse,
        level: LEVEL[currentCourse.level],
        skills: getListData(skills),
        files:
          currentCourse && !!currentCourse.coverUrl
            ? [
                {
                  url: currentCourse.coverUrl
                }
              ]
            : []
      });
    }
  }

  componentWillReceiveProps(nextProps) {
    const { currentCourse } = nextProps;
    this.setState({
      files:
        currentCourse && !!currentCourse.coverUrl
          ? [
              {
                url: currentCourse.coverUrl
              }
            ]
          : []
    });
  }

  render() {
    const {
      description,
      requirement,
      target,
      isFirstChange,
      skills,
      files,
      level
    } = this.state;

    const listSkills = getListData(this.props.listSkills);

    return (
      <form
        className="needs-validation"
        noValidate
        onSubmit={this.handleSubmit}
      >
        <div className="container ">
          <div className="bg-white px-5 py-3 mb-3">
            {/** Main description */}
            <Title data="1. Main description" />

            <Label data="Course Thumbnail" />
            <Dropzone
              accept="image/*"
              onDrop={this.onDrop}
              onCancel={this.onCancel}
              type="image"
              files={files}
              multiple={false}
            />
            <div className="form-group">
              <Label data="Describe your course here" required />
              <CKEditor
                isFirstChange={isFirstChange["description"]}
                content={description}
                handleChange={this.handleChangeEditor}
              />
            </div>

            {/** tags */}
            {/** <Input
              label="Set course tags"
              name="tag"
              handleChange={this.handleChange}
              value={tag}
              placeholder="Ex: java, php, android"
              required
            /> */}
            <hr className="hr-sm-out my-5" />
            {/** end Main description */}

            {/** Quick extra info */}
            <Title data="2. Quick extra info" />
            <div className="row mt-4">
              <div className="col-6">
                <Select
                  label="Course category"
                  required
                  name="skills"
                  value={skills}
                  data={listSkills}
                  handleChange={this.handleChangeSelect}
                  isFirstChange={isFirstChange["skills"]}
                  isMulti
                />
              </div>

              <div className="col-6">
                <Select
                  label="Student skill level required"
                  required
                  name="level"
                  value={level}
                  defaultValue={level}
                  data={data["levels"]}
                  isFirstChange={isFirstChange["level"]}
                  handleChange={this.handleChangeSelect}
                />
              </div>

              {/**<div className="col-6">
                <Select
                  label="Course language"
                  required
                  name="language"
                  value={language}
                  data={data["languages"]}
                  isFirstChange={isFirstChange}
                  handleChange={this.handleChangeSelect}
                />
              </div> */}
            </div>

            <div className="row">
              <div className="col-6">
                <MultiInput
                  values={requirement}
                  name="requirement"
                  label="Knowledge Requirements"
                  desc="What your student should already know before enrolling"
                  checkAtLeast={checkAtLeast(requirement)}
                  handleChange={this.handleChangeMulti}
                />
              </div>
              {/**  <div className="col-6">
                <MultiInput
                  name="archievement"
                  label="Course Achievements"
                  desc="What your students will learn after completing your course"
                  checkAtLeast={checkAtLeastArchievement}
                  handleChange={this.handleChange}
                />
              </div> */}
              <div className="col-6">
                <MultiInput
                  name="target"
                  values={target}
                  label="Target Students"
                  desc="What students would be interested in your course"
                  checkAtLeast={checkAtLeast(target)}
                  handleChange={this.handleChangeMulti}
                />
              </div>
            </div>
            <div className="text-center">
              <Button type="submit">Save</Button>
            </div>
          </div>
          {/** end Quick extra info */}
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  undefined,
  { updateCourseRequest }
)(Index);

export default withRouter(connectIndex);
