const defaultValue = [];
export const jobTypeData = [
  {
    label: "Full-time (40 hrs/wk)"
  },
  {
    label: "Hourly contract"
  },
  {
    label: "Fixed price"
  }
].map((item, i) => {
  defaultValue[`jobType${i + 1}`] = false;
  return {
    value: i + 1,
    label: item.label
  };
});

export const jobTypeDefault = defaultValue;
