import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter, Redirect } from "react-router-dom";
import { getCourseRequest } from "store/Instructor/action/course";
import {
  getSectionRequest,
  setCurrentSection,
  toggleSection
} from "store/Instructor/action/section";
import Section from "../components/Form/Section";
import Loading from "components/Loading";
import Default from "./Default";

class Index extends Component {
  componentDidMount() {
    const {
      match: {
        params: { sectionId }
      },
      currentSection,
      getSectionRequest,
      toggleSection
    } = this.props;

    if (!currentSection && sectionId) {
      getSectionRequest(sectionId);
      toggleSection(sectionId);
    }
  }

  render() {
    const {
      currentCourse,
      match: {
        url,
        params: { sectionId }
      }
    } = this.props;

    if (!currentCourse) {
      return <Loading />;
    } else if (!sectionId) {
      if (currentCourse.sections.length > 0) {
        return (
          <Redirect to={`${url}/section/${currentCourse.sections[0].id}`} />
        );
      }
      return <Default />;
    }

    return <Section label="Edit section" />;
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse, currentSection } }) => ({
    currentCourse,
    currentSection
  }),
  { getCourseRequest, getSectionRequest, setCurrentSection, toggleSection }
)(Index);

export default withRouter(connectIndex);
