import { call, put, takeLatest } from "redux-saga/effects";
import { GetListSkills } from "store/api/Version1";
import {
  getListSkillsFailure,
  getListSkillsSuccess,
  GET_LIST_SKILLS_REQUEST
} from "./action";

//create
function* getListSkills() {
  try {
    const { data } = yield call(GetListSkills);
    yield put(getListSkillsSuccess(data));
  } catch (error) {
    yield put(getListSkillsFailure(error));
  }
}

function* watchGetListSkills() {
  yield takeLatest(GET_LIST_SKILLS_REQUEST, getListSkills);
}
export default [watchGetListSkills()];
