import React, { Component, Fragment } from "react";
import Card from "components/WithMentor/Course/Card";
import { Link, withRouter } from "react-router-dom";
import screenSize from "util/ScreenSize";
import withSizes from "react-sizes";
import { getSearchParams } from "util/Common";
import { connect } from "react-redux";
import { getCoursesJoinRequest } from "store/Course/action";
import Loading from "components/Loading";
import Pagination from "components/Pagination";
import WithMentor from "components/WithMentor";
import { SearchInput } from "components/Field";

const defaultSize = 4;

class Index extends Component {
  state = {
    search: ""
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  componentDidMount() {
    const params = getSearchParams(this.props);
    const page = params.get("page");

    this.getCourses({ page });
  }

  componentDidUpdate() {
    window.scrollTo(0, 0);
  }

  getCourses = ({ page }) => {
    const { getCoursesJoinRequest } = this.props;
    getCoursesJoinRequest({
      params: {
        page: page - 1 || 0,
        size: defaultSize
      }
    });
  };

  handleSearch = () => {
    this.getCourses({
      page: 2,
      cate: ""
    });
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    const nextParams = getSearchParams(nextProps);
    const params = getSearchParams(this.props);

    const page = nextParams.get("page");
    if (page !== params.get("page")) {
      this.getCourses({ page });
    }
  }

  render() {
    const {
      listCourses,
      isLoadingCourse,
      match: { url }
    } = this.props;
    const isShow = isLoadingCourse || listCourses.length === 0;
    if (isShow) {
      return <Loading />;
    }
    const coursesData = listCourses.content;
    return (
      <Fragment>
        <div className="text-center z-depth-1 position-relative">
          <div className="col-md-8 col-12 offset-md-2 col-xl-6 offset-xl-3">
            <SearchInput
              name="search"
              value={this.state.search}
              handleChange={this.handleChange}
              handleSearch={this.handleSearch}
            />
          </div>
        </div>
        <div className="container">
          <div>
            <h2 className="text-center font-weight-normal h2-responsive pt-4">
              My courses
            </h2>
          </div>
          <div className="row my-5">
            <div className="col-md-12 align-self-center">
              <div className="row ">
                {coursesData.map((item, i) => (
                  <div
                    className="col-xl-3 col-md-6 col-lg-6 col-12 mt-md-0 mb-4"
                    key={i}
                  >
                    <Link
                      to={{
                        pathname: `/course/${item.id}/overview`,
                        state: { url }
                      }}
                      replace
                      className="cursor-pointer"
                    >
                      <Card data={item} />
                    </Link>
                  </div>
                ))}
              </div>
              {coursesData.length === 0 && (
                <h4 className="h4-responsive text-center">
                  No course be found
                </h4>
              )}
              <div className="row justify-content-center mt-3">
                <Pagination
                  total={listCourses.totalPages}
                  current={listCourses.page + 1}
                  canPrev={listCourses.page !== 0}
                  canNext={!listCourses.last}
                />
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}
const mapSizesToProps = ({ width }) => screenSize(width);

const connectIndex = connect(
  ({ course: { isLoading: isLoadingCourse, list: listCourses } }) => ({
    isLoadingCourse,
    listCourses
  }),
  { getCoursesJoinRequest }
)(Index);

const withSizesIndex = withSizes(mapSizesToProps)(connectIndex);
export default WithMentor(withRouter(withSizesIndex));
