import React from "react";
import { Link } from "react-router-dom";
import { Button } from "mdbreact";
import { logout } from "store/Auth/action";
import { connect } from "react-redux";
import {
  FaSignOutAlt,
  FaInfoCircle,
  FaUserGraduate,
  FaBook
} from "react-icons/fa";
import classNames from "classnames";

const Index = ({ logout }) => (
  <div>
    <Link to={"/instructor/create/course"}>
      <Button className={classNames("p-3 font-weight-normal w-100")}>
        Create course
      </Button>
    </Link>
    <Link to={"/my-courses"}>
      <div className={classNames("p-3 font-weight-normal")}>
        <FaUserGraduate size={20} /> <span className="ml-2">My course</span>
      </div>
    </Link>
    <Link to={"/profiles"}>
      <div className={classNames("p-3 font-weight-normal")}>
        <FaInfoCircle size={20} /> <span className="ml-2">My profile</span>
      </div>
    </Link>
    <Link to={"/instructor"}>
      <div className={classNames("p-3 font-weight-normal")}>
        <FaBook size={20} /> <span className="ml-2">Instructor area</span>
      </div>
    </Link>
    <div className={classNames("p-3 font-weight-normal")} onClick={logout}>
      <FaSignOutAlt size={20} /> <span className="ml-2">Logout</span>
    </div>
  </div>
);

const connectIndex = connect(
  undefined,
  { logout }
)(Index);

export default connectIndex;
