import { TOGGLE_NAVBAR, HOVER_NAVBAR } from "./action";

const initialState = {
  isShowNavResponsive: false,
  navIndex: null,
  isShowNav: true,
  navSubIndex: null,
  isShowNavSub: false
};

export default (state = initialState, action) => {
  switch (action.type) {
    case HOVER_NAVBAR:
      return {
        ...state,
        ...action.payload.params
      };
    case TOGGLE_NAVBAR:
      return {
        ...state,
        isShowNavResponsive: !state.isShowNavResponsive
      };
    default:
      return state;
  }
};
