import { HANDLE_SCROLL } from './action';

const initialState = {
  fixed: false,
  bottom: 0,
}
export default (state = initialState, action) => {
  switch (action.type) {
    case HANDLE_SCROLL: 
      const bottom = action.payload.argurments['bottom'];
      console.log(bottom);
      return {
        ...state,
        bottom: bottom,
        fixed: bottom < 0
      }

    default:
      return state
  }
}