import React, { Component } from "react";
import { Input } from "components/Field";
import Title from "pages/Course/components/Title";
import { Button } from "mdbreact";
import { connect } from "react-redux";
import {
  setCurrentSection,
  createSectionRequest,
  updateSectionRequest,
  toggleSection
} from "store/Instructor/action/section";
import { getCourseRequest } from "store/Instructor/action/course";
import { withRouter } from "react-router-dom";

class Index extends Component {
  constructor() {
    super();
    this.state = {
      title: ""
    };
  }

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    const {
      isCreate,
      createSectionRequest,
      updateSectionRequest,
      currentCourse,
      currentSection,
      history
    } = this.props;
    const { title } = this.state;

    if (isCreate) {
      createSectionRequest({ title }, history);
    } else {
      // update section
      updateSectionRequest({
        courseId: currentCourse.id,
        id: currentSection.id,
        params: { title },
        history
      });
    }
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.currentSection) this.setState(nextProps.currentSection);
  }

  componentDidMount() {
    const { currentSection } = this.props;

    if (currentSection) {
      if (!this.state.title) {
        this.setState(currentSection);
      }
    }
  }

  render() {
    const { title } = this.state;
    const { label } = this.props;

    return (
      <form
        className="needs-validation"
        noValidate
        onSubmit={this.handleSubmit}
      >
        {/** basic info */}
        <div className="bg-white p-3 mb-3">
          <Title data={label} />
          <div className="container">
            <Input
              label="Section title"
              name="title"
              handleChange={this.handleChange}
              value={title}
              autoFocus
              required
            />
            <div className="text-center">
              <Button type="submit">Save</Button>
            </div>

            {/** end basic info */}
          </div>
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse, currentSection } }) => ({
    sections: currentCourse ? currentCourse.sections : [],
    currentSection,
    currentCourse
  }),
  {
    setCurrentSection,
    createSectionRequest,
    updateSectionRequest,
    getCourseRequest,
    toggleSection
  }
)(Index);

export default withRouter(connectIndex);
