import React, { Component } from "react";
import style from "./style.scss";
import Sidebar from "./components/Sidebar";
import screenSize from "util/ScreenSize";
import withSizes from "react-sizes";
import SidebarResponsive from "./components/Sidebar/Responsive";
import data from "./data";
import { withRouter, Redirect } from "react-router-dom";
import { getCourseRequest } from "store/Course/action";
import Loading from "components/Loading";
import { connect } from "react-redux";

export default App => {
  class Layout extends Component {
    componentDidMount() {
      const {
        match: {
          params: { id }
        },
        location: { state },
        course,
        getCourseRequest
      } = this.props;

      // eslint-disable-next-line
      if (!course || course.id != id || (state && state.urlFrom === "enroll")) {
        getCourseRequest(id);
      }
    }

    render() {
      const {
        isDesktop,
        isExtraSmall,
        isLoading,
        course,
        location: { state },
        match: {
          params: { id }
        }
      } = this.props;
      if (isLoading || !course) return <Loading />;

      if (!course.join) {
        return <Redirect to={`/course/${id}/enroll`} />;
      }

      console.log(this.props);
      const lectureId = course.sections[0].lectures[0].id;
      const urlBack = state && state.url ? state.url : "/course";
      return (
        <div className="d-flex">
          <div>
            {isDesktop ? (
              <Sidebar data={data} urlBack={urlBack} lectureId={lectureId} />
            ) : (
              <SidebarResponsive
                lectureId={lectureId}
                data={data}
                isExtraSmall={isExtraSmall}
                urlBack={urlBack}
              />
            )}
          </div>
          <div className={style.content}>
            <App course={course} lectureId={lectureId} />
          </div>
        </div>
      );
    }
  }
  const mapSizesToProps = ({ width }) => screenSize(width);

  const sizesLayout = withSizes(mapSizesToProps)(Layout);

  const connectLayout = connect(
    ({ course: { current: course, isLoading } }) => ({ course, isLoading }),
    {
      getCourseRequest
    }
  )(sizesLayout);

  return withRouter(connectLayout);
};
