import React from "react";
import { Button } from "mdbreact";

export default ({ title, callback }) => (
  <div className="d-flex justify-content-between align-items-center mt-3 mb-2">
    <div className="font-weight-bold">{title}</div>
    <div>
      <Button
        outline
        size="sm"
        onClick={() => {
          callback();
        }}
      >
        Clear
      </Button>
    </div>
  </div>
);
