import React, { Component } from "react";
import style from "./style.scss";
import classnames from "classnames";
import { Link } from "react-router-dom";
import { Button } from "mdbreact";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { Input } from "components/Field";
import { registerRequest } from "store/Auth/action";
import * as EmailValidator from 'email-validator';

class Index extends Component {
  state = {
    fname: {
      value: '',
      valid: null
    },
    username: {
      value: '',
      valid: null
    },
    email: {
      value: '',
      valid: null
    },
    password: {
      value: '',
      valid: null
    },
    confirmPass: {
      value: '',
      valid: null
    },
    acceptTerm: {
      value: false,
      valid: null
    }
  };

  validateForm() {
    return  Object.values(this.state).every((item)=>item.valid);
  }

  submitHandler = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    if (this.validateForm()) {
      const { fname, username, email, password } = this.state;
      const { history, registerRequest, location } = this.props;
  
      const prevPath = (location.state && location.state.from.pathname) || "/";
      registerRequest({
        params: {
          fname: fname.value,                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                           
          username: username.value,
          email: email.value,                                           
          password: password.value
        },
        history,
        url: prevPath
      });
    }
  };

  changehandlerInput = (name, value ) => {
    const { password, confirmPass } = this.state;
    switch(name) {
      case "email": {
        this.setState({
          ...this.state,
          [name]: { value, valid: EmailValidator.validate(value) }
        })
        break;
      }
      case "password": 
        this.setState({
          ...this.state,
          [name]: { value, valid: value.length>=6 && value.length<=20 },
          confirmPass: { ...this.state.confirmPass, valid: value===confirmPass.value && value!=='' }
        })
        break;
      case "confirmPass": 
        this.setState({
          ...this.state,
          [name]: { value, valid: password.value===value && value!==''}
        })
        break;  
      default: 
        this.setState({
          ...this.state,
          [name]: { value, valid: !!value }
        })
    }
  }
  changeHandler = event => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.changehandlerInput(name, value);
  };

  render() {
    const { 
      fname, 
      username, 
      email, 
      password, 
      confirmPass, 
      acceptTerm 
    } = this.state;

    // get valid or invalid classname
    const getClassName = (field) =>  field.valid == null ? '' : (field.valid ? 'is-valid valid' : 'is-invalid invalid');

    return (
      <form
        onSubmit={this.submitHandler}
        noValidate
      >
        <h4 className={"text-center font-weight-500 my-4"}>
          Get Hired, Pay 0% Fees
        </h4>

        <Input
          label="Full name" 
          value={fname.value}
          handleChange={this.changeHandler}
          type="text"
          name="fname"
          placeholder="Enter fullname"
          invalidText="Please provide a valid name."
          validText="Look's good"
          className={getClassName(fname)}
          required        
        />
        
        <Input 
          label="User name"
          value={username.value}
          handleChange={this.changeHandler}
          type='text'
          name='username'
          placeholder="Enter username"
          invalidText="Please provide a valid username."
          validText="Very awesome"
          className={getClassName(username)}
          required
        />

        <Input
          label="Email"
          value={email.value}
          handleChange={this.changeHandler}
          type="email"
          name="email"
          placeholder="name@domain.com" 
          invalidText="Please provide a valid email."
          validText="Email is valid"
          className={getClassName(email)}
          required
        />

        <Input 
          label="Password"
          value={password.value}
          handleChange={this.changeHandler}
          type="password"
          name="password"
          placeholder="********"
          minLength={6}
          maxLength={20}
          invalidText="Your password must be between 6 and 20 characters"
          validText="Password is strong"
          className={getClassName(password)}
          required
        />

        <Input 
          label='Confirm Password'
          value={confirmPass.value}
          handleChange={this.changeHandler}
          type='password'
          name='confirmPass'
          placeholder="********"
          invalidText="Confirm Password must be same as password."
          validText="Match password"
          className={getClassName(confirmPass)}
          required
        />

        <div className={style["check-box-container"]}>
          <input
            type="checkbox"
            id="acceptTerm"
            checked={acceptTerm.value}
            onChange={this.changeHandler}
            name="acceptTerm"
            className={classnames(
              "form-control form-check-input",
              getClassName(acceptTerm)
            )}
            required
          />
          <label
            className="form-check-label"
            htmlFor="acceptTerm"
          >
            I accept the{" "}
            <Link
              className={"main-color font-weight-500"}
              to="/term"
            >
              Terms of Service
            </Link>
          </label>

          <div className="invalid-feedback">
            You must agree before submitting.
          </div>
        </div>

        <div className={"width mid-container my-3"}>
          <Button
            className={"width mx-0"}
            style={{ fontSize: "1.15rem" }}
            color={"success"}
            type="submit"
          >
            Create Account
          </Button>
        </div>
      </form>
    )
  }
}

const connectIndex = connect(
  undefined,
  { registerRequest }
)(Index);

export default withRouter(connectIndex);