import React, {Component} from "react";
import {Button, Container} from "mdbreact";
import LeftSide from "./leftSide/index";
import MiddleSide from "./middleSide/index";
import RightSide from "./rightSide/index";
import style from './style.scss'

class Index extends Component {
    constructor() {
        super();
        this.state = {
            textSearch: "",
            skills: ""
        };

        this.onClickSearch = this.onClickSearch.bind(this);
        this.keyPress = this.keyPress.bind(this);
        this.handleChangeSearch = this.handleChangeSearch.bind(this);
    }

    handleChangeSearch(e) {
        this.setState({
            textSearch: e.target.value
        });
    }

    onClickSearch() {
        console.log("search");
    }

    keyPress(e) {
        if (e.keyCode === 13) {
            this.onClickSearch();
        }
    }

    render() {
        return (
            <Container className={'mt-4 mb-5'}>
                {/* search class search*/}
                <div className='pt-5 pt-sm-0'></div>
                <div className={"d-flex width " + style.searchContainer}>
                    <input
                        placeholder="Search jobs by title or keywords"
                        onChange={this.handleChangeSearch}
                        onKeyDown={this.keyPress}/>
                    <Button
                        color="primary"
                        className={'m-0 p-0'}
                        style={{width: '170px', borderRadius: '0'}}
                        onClick={this.onClickSearch}>
                        Search jobs
                    </Button>
                </div>
                {/* end search */}

                <div className="h3 my-4">Search Jobs</div>
                <div className="row">
                    <div className="col-md-4 col-lg-3 pt-1">
                        <LeftSide/>
                    </div>
                    <div className="col-12 col-md-8 col-lg-6 bg-white pt-3">
                        <MiddleSide/>
                    </div>
                    <div className="col-lg-3">
                        <RightSide/>
                    </div>
                </div>
            </Container>
        );
    }
}

export default Index;
