import {
  SET_CURRENT_LECTURE,
  CREATE_LECTURE_FAILURE,
  CREATE_LECTURE_REQUEST,
  CREATE_LECTURE_SUCCESS,
  UPDATE_LECTURE_FAILURE,
  UPDATE_LECTURE_REQUEST,
  UPDATE_LECTURE_SUCCESS,
  GET_LECTURE_FAILURE,
  GET_LECTURE_REQUEST,
  GET_LECTURE_SUCCESS,
  ORDER_LECTURE_FAILURE,
  ORDER_LECTURE_REQUEST,
  ORDER_LECTURE_SUCCESS,
  DELETE_LECTURE_FAILURE,
  DELETE_LECTURE_REQUEST,
  DELETE_LECTURE_SUCCESS
} from "../action/lecture";
import { checkTokenExpired } from "util/Common";

export default (state, action) => {
  switch (action.type) {
    case SET_CURRENT_LECTURE:
      return {
        newState: {
          ...state,
          currentLecture: action.payload.lecture
            ? {
                ...action.payload.lecture
              }
            : null
        },
        isNewState: true
      };
    // create
    case CREATE_LECTURE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          sectionId: action.payload.sectionId,
          params: action.payload.params,
          history: action.payload.history
        },
        isNewState: true
      };
    case CREATE_LECTURE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentLecture: {
            ...action.payload.lecture
          }
        },
        isNewState: true
      };
    // get
    case GET_LECTURE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          id: action.payload.id
        },
        isNewState: true
      };
    case GET_LECTURE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentLecture: {
            ...action.payload.lecture
          },
          collapse: {
            ...state.collapse,
            [action.payload.lecture.sectionId]: true
          }
        },
        isNewState: true
      };

    // update
    case UPDATE_LECTURE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case UPDATE_LECTURE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentLecture: {
            ...state.currentLecture,
            ...action.payload.data
          }
        },
        isNewState: true
      };

    // order
    case ORDER_LECTURE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case ORDER_LECTURE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false
        },
        isNewState: true
      };

    // DELETE
    case DELETE_LECTURE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case DELETE_LECTURE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false
        },
        isNewState: true
      };

    // failure
    case ORDER_LECTURE_FAILURE:
    case CREATE_LECTURE_FAILURE:
    case GET_LECTURE_FAILURE:
    case DELETE_LECTURE_FAILURE:
    case UPDATE_LECTURE_FAILURE: {
      const error = action.payload.error;
      checkTokenExpired(error);

      return {
        newState: {
          ...state,
          isLoading: false,
          error: action.payload.error
        },
        isNewState: true
      };
    }
    default:
      return { newState: state, isNewState: false };
  }
};
