import React from "react";
import { convertDatatoBytes } from "util/Common";
import style from "./style.scss";
import Video from "components/Video";
import { FaRegTrashAlt } from "react-icons/fa";
import classNames from "classnames";

const ViewInfo = ({ file, onCancel }) => {
  let isViewName = false;
  let newSize, type;
  if (file && file.size && file.name) {
    isViewName = true;
    const convert = convertDatatoBytes(file.size);
    newSize = convert.size;
    type = convert.type;
  }

  return (
    <div className="word-break text-center my-3 font-weight-normal">
      {isViewName ? (
        <span>
          {file.name} - {newSize} {type}
        </span>
      ) : (
        <span />
      )}
      <span
        className="p-2 cursor-pointer align-middle btn-outline-hover"
        onClick={onCancel}
      >
        <FaRegTrashAlt />
      </span>
    </div>
  );
};

const PreviewImage = ({ file: { url, name } }) => (
  <div>
    {url ? (
      <div className={classNames(style.thumbnail, "p-1 border")}>
        <img className="h-100" src={url} alt={name} />
      </div>
    ) : (
      <div />
    )}
  </div>
);

const PreviewVideo = ({ file, height, width }) => (
  <div className="p-1 border">
    <Video
      height={height}
      width={width}
      src={file && file.url ? file.url : ""}
    />
  </div>
);

const Preview = ({ file, type, width, height }) => (
  <div>
    {type === "video" ? (
      <PreviewVideo file={file} height={height} width={width} />
    ) : (
      <PreviewImage file={file} />
    )}
  </div>
);

export default ({
  files,
  type = "image",
  isPreview = true,
  width,
  height,
  onCancel
}) => (
  <div className="d-flex align-self-center justify-content-center">
    {files.map((file, index) => (
      <div key={index} className="d-flex flex-column">
        <div>
          {isPreview && (
            <Preview height={height} width={width} file={file} type={type} />
          )}
        </div>
        <ViewInfo file={file} onCancel={onCancel} />
      </div>
    ))}
  </div>
);
