import { fork, call, take, put, takeEvery } from "redux-saga/effects";
import { Login, Register } from "store/api/Version1";
import {
  loginSuccess,
  loginFailure,
  logout,
  registerSuccess,
  registerFailure,
  LOGIN_REQUEST,
  LOGIN_FAILURE,
  LOGOUT,
  REGISTER_REQUEST
} from "./action";

function* authorize({ params, history, url }) {
  try {
    const {
      data: { accessToken }
    } = yield call(Login, params);
    yield put(loginSuccess(accessToken));
    history.push(url);
  } catch (error) {
    console.log(error);
    yield put(loginFailure(error.response));
  }
}

function* loginFlow() {
  while (true) {
    const { payload } = yield take(LOGIN_REQUEST);
    yield fork(authorize, payload);
    yield take([LOGOUT, LOGIN_FAILURE]);
    yield call(logout);
  }
}

function* register(data) {
  try {
    yield call(Register, data);
    yield put(registerSuccess);
  } catch (error) {
    yield put(registerFailure(error.response));
  }
}

function* watchRegister() {
  yield takeEvery(REGISTER_REQUEST, register);
}

export default [loginFlow(), watchRegister()];
