import React from "react";
import Dropzone from "react-dropzone";
import style from "./style.scss";
import classNames from "classnames";
import ViewFile from "./ViewFile";

export default ({
  label = "Drop files here, or click to select files",
  styleCss = "",
  onDrop,
  onCancel,
  type,
  files = [],
  width = "auto",
  height = 400,
  ...props
}) => {
  try {
    files = files.map(file => {
      if (!file.url) {
        Object.assign(file, {
          url: URL.createObjectURL(file)
        });
      }
      return file;
    });
  } catch (err) {
    console.log("Error add url file");
  }
  return (
    <div>
      <Dropzone onDrop={onDrop} onFileDialogCancel={onCancel} {...props}>
        {({ getRootProps, getInputProps }) => (
          <div
            className={classNames(
              style.content,
              files.length !== 0
                ? "d-none"
                : "d-flex justify-content-center align-items-center my-3",
              styleCss
            )}
            {...getRootProps()}
          >
            <input {...getInputProps()} />
            <div>{label}</div>
          </div>
        )}
      </Dropzone>
      <ViewFile
        type={type}
        files={files}
        vidth={width}
        height={height}
        onCancel={onCancel}
      />
    </div>
  );
};
