import ReducerCourse from "./course";
import ReducerLecture from "./lecture";
import ReducerSection from "./section";

const initialState = {
  params: null,
  courses: [],
  currentCourse: null,
  currentSection: null,
  collapse: null,
  currentLecture: null,
  isLoading: false,
  error: null
};

const reducer = [ReducerCourse, ReducerLecture, ReducerSection];

export default (state = initialState, action) => {
  let result = state;
  for (let i = 0; i < reducer.length; i++) {
    let { newState, isNewState } = reducer[i](state, action);
    if (isNewState) {
      result = newState;
      break;
    }
  }

  return result;
};
