import React, { Component } from "react";
import { Container, Row, Col, Button } from "mdbreact";
import { Helmet } from "react-helmet";
import classnames from "classnames";
import RequiredInput from "components/RequiredInput";
import cities from "./cities";
import SimpleSelect from "components/SimpleSelect";

class PostJob extends Component {
  constructor(props) {
    super(props);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleChangeSelect = this.handleChangeSelect.bind(this);
  }

  state = {
    first: 0,
    job: {
      title: "",
      location: "DANANG",
      content: "",
      salary: "",
      jobType: "FREE",
      skillIdList: ""
    },
    agency: {
      name: "",
      website: "",
      location: "DANANG",
      description: "",
      slogan: ""
    }
  };

  submitHandler(event) {
    event.preventDefault();
    this.setState({
      first: 1
    });
    console.log(this.state);
  }

  changeHandler = event => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    let formName = this.state.job.hasOwnProperty(name) ? "job" : "agency";
    this.setState({
      ...this.state,
      [formName]: {
        ...this.state[formName],
        [name]: value
      }
    });
  };

  handleChangeSelect = name => value => {
    this.setState({
      ...this.state,
      job: {
        ...this.state.job,
        [name]: value
      }
    });
  };

  render() {
    const state = this.state;
    const job = state.job;
    const agency = state.agency;
    return (
      <Container className={"pt-3 pb-5"}>
        <Helmet>
          <title>Post Jobs</title>
        </Helmet>
        <Row>
          <Col xs={"12"} md={"8"}>
            <div className={"page px-4 py-4"} style={{ overflow: "visible" }}>
              <h4>Post a remote job (FREE)</h4>
              <h6 className={"blur font-size-14 mb-3"}>
                Hubstaff Talent is for jobs that can be worked on remotely.
                Learn more...
              </h6>
              <h6
                className={
                  "font-weight-500 font-size-14 pb-3 mb-3 bottom-border-blur"
                }
              >
                TELL US ABOUT YOUR JOB
              </h6>
              <form
                className={classnames(
                  { "was-validated": !!state.first },
                  "needs-validation"
                )}
                onSubmit={this.submitHandler.bind(this)}
                noValidate
              >
                <RequiredInput
                  field={job.title}
                  id={"title"}
                  label={"Job Title"}
                  onChange={this.changeHandler}
                  first={state.first}
                />
                {/*<SimpleSelect field={job.location} first={state.first} id={'location'} label={'Location'}*/}
                {/*placeholder={'Your City'} handleChange={this.handleChangeSelect}*/}
                {/*options={cities}/>*/}
                <RequiredInput
                  field={job.content}
                  id={"content"}
                  label={"Description"}
                  type={"textarea"}
                  onChange={this.changeHandler}
                  first={state.first}
                />
                <RequiredInput
                  field={job.salary}
                  id={"salary"}
                  label={"Salary"}
                  type={"number"}
                  onChange={this.changeHandler}
                  first={state.first}
                  unit={"Million VND"}
                  placeholder={"1 million dollar"}
                />

                {/*<SimpleSelect field={job.jobType} first={state.first} id={'jobType'} label={'Job Type'}*/}
                {/*placeholder={'Chicken dinner'} handleChange={this.handleChangeSelect}*/}
                {/*options={cities}/>*/}
                <SimpleSelect
                  field={job.skillIdList}
                  first={state.first}
                  id={"skillIdList"}
                  label={"Skills"}
                  placeholder={"List skills of your job"}
                  handleChange={this.handleChangeSelect}
                  options={cities}
                  isMulti={true}
                />

                <h6
                  className={
                    "font-weight-500 font-size-14 pb-3 mb-3 mt-4 bottom-border-blur"
                  }
                >
                  TELL US A LITTLE ABOUT YOUR COMPANY
                </h6>
                <h6 className={"blur font-size-14 mb-3"}>
                  The more information you provide the better your chances of
                  attracting candidates.
                </h6>

                <RequiredInput
                  field={agency.name}
                  id={"name"}
                  label={"Company Name"}
                  onChange={this.changeHandler}
                  first={state.first}
                />
                <RequiredInput
                  field={agency.website}
                  id={"website"}
                  label={"Website"}
                  onChange={this.changeHandler}
                  first={state.first}
                />
                <RequiredInput
                  field={agency.description}
                  id={"description"}
                  label={"Description"}
                  onChange={this.changeHandler}
                  first={state.first}
                  type={"textarea"}
                />
                <RequiredInput
                  field={agency.slogan}
                  id={"slogan"}
                  label={"Slogan"}
                  onChange={this.changeHandler}
                  first={state.first}
                />

                <div className={"width d-flex"}>
                  <Button className={"ml-auto"} color={"success"} type="submit">
                    Post your Job
                  </Button>
                </div>
              </form>
            </div>
          </Col>
          <Col xs={"12"} md={"4"}>
            <div className={"page px-4 py-4"}>
              <h5 className={"font-weight-500"}>FAQ</h5>
              {faq.map((ele, index) => (
                <div key={index} className={"font-size-14"}>
                  <div className={"mt-2"}>
                    <strong className={"main-color"}>Q:&nbsp;</strong>
                    <span className={"font-weight-500"}>{ele.q}</span>
                  </div>
                  <div>
                    <strong className={"main-color"}>A:&nbsp;</strong>
                    <span className={"blur"}>{ele.a}</span>
                  </div>
                </div>
              ))}
            </div>
          </Col>
        </Row>
      </Container>
    );
  }
}

const faq = [
  {
    q: "Is there a fee to post a job?",
    a: `There are no fees for posting a job on Hubstaff Talent. It is a 100% free service that we offer both for 
        the employer and the contractor.`
  },
  {
    q: "How do I find contractors for my job?",
    a: `Posting a job on Hubstaff Talent will get your project in front of the most qualified contractors and 
        agencies. You will then get applications for the job with the applicants details and reasons why they are the
         best fit for the job. You can also search for contractors and invite them to apply.`
  }
];

export default PostJob;
