import React from "react";
import classNames from "classnames";
import { Link, withRouter } from "react-router-dom";
import style from "./style.scss";
import { checkCurrentUrl } from "util/Common";
import getItemUrl from "./getItemUrl";

const Index = ({
  data,
  isExtraSmall,
  match: {
    url,
    params: { id }
  },
  lectureId,
  urlBack,
  location: { state }
}) => {
  return (
    <div
      className={classNames(
        style.sideBarResponsive,
        "text-center row m-0 justify-content-center align-items-center"
      )}
    >
      {data.map((item, i) => {
        let itemUrl = getItemUrl({ i, id, lectureId, urlBack, item });
        return (
          <div key={i} className="col-3 border-left ">
            <Link
              to={itemUrl}
              className={classNames(
                "text-black-50",
                checkCurrentUrl(item.url, url) && "text-primary"
              )}
            >
              <span className={style.icon}>{item.icon}</span>
              {!isExtraSmall && (
                <span className="font-weight-bold ml-3">{item.tooltip}</span>
              )}
            </Link>
          </div>
        );
      })}
    </div>
  );
};

export default withRouter(Index);
