import React, { Component, Fragment } from "react";
import { withRouter } from "react-router-dom";
import { Route, Redirect } from "react-router-dom";
import { TopProfile } from "components/WithMentor/Top";
import withMentor from "components/WithMentor";
import Home from "./Home";
import Follow from "./Follow";
import Review from "./Review";
import Recommendation from "./Recommendation";

class Index extends Component {
  render() {
    const { match } = this.props;
    const username = "thuc";
    return (
      <Fragment>
        <Route
          exact
          path={`${match.path}`}
          render={() => <Redirect to={`${match.path}/${username}`} />}
        />
        <Route exact path={`${match.path}/:username`} component={Home} />
        <Route
          exact
          path={`${match.path}/:username/reviews`}
          component={Review}
        />
        <Route
          exact
          path={`${match.path}/:username/followers`}
          component={Follow}
        />
        <Route
          exact
          path={`${match.path}/:username/recommendations`}
          component={Recommendation}
        />
      </Fragment>
    );
  }
} 

export default withMentor(withRouter(Index), { Top: TopProfile });
