import "./style.scss";
import React from "react";

export default props => {
  if (props.error) {
    return (
      <div>
        Error! <button onClick={props.retry}>Retry</button>
      </div>
    );
  } else {
    return (
      <div className="position-fixed w-100 h-100 d-flex justify-content-center align-items-center div-loading">
        <div className="loading style-2" />
      </div>
    );
  }
};
