import React, { Fragment } from "react";
import { withRouter } from "react-router-dom";
import { Route } from "react-router-dom";
import AddLecture from "./Lecture/Add";
import AddSection from "./Section/Add";
import EditLecture from "./Lecture/Edit";
import EditSection from "./Section/Edit";
import Layout from "./components/Layout";

const Index = ({ match }) => {
  return (
    <Fragment>
      <Route exact path={`${match.path}`} component={EditSection} />
      <Route path={`${match.path}/create/lecture`} component={AddLecture} />
      <Route path={`${match.path}/create/section`} component={AddSection} />
      <Route
        path={`${match.path}/lecture/:lectureId`}
        component={EditLecture}
      />
      <Route
        path={`${match.path}/section/:sectionId`}
        component={EditSection}
      />
    </Fragment>
  );
};

export default Layout(withRouter(Index));
