import React, { Fragment } from "react";
import { MDBPageItem, MDBPageNav } from "mdbreact";
import Default from "./Default";

const PaginationPage = ({ url, total, current }) => {
  return (
    <Fragment>
      <Default url={url} total={1} />

      <MDBPageItem disabled>
        <MDBPageNav className="page-link" aria-label="Previous">
          <span aria-hidden="true"></span>
        </MDBPageNav>
      </MDBPageItem>

      <Default
        url={url}
        total={3}
        start={current === 4 ? current : current - 1}
        current={current}
      />

      <MDBPageItem disabled>
        <MDBPageNav className="page-link" aria-label="Previous">
          <span aria-hidden="true"></span>
        </MDBPageNav>
      </MDBPageItem>

      <Default url={url} total={1} start={total} />
    </Fragment>
  );
};

export default PaginationPage;
