import _ from "lodash";

export const saveToken = key => {
  localStorage.setItem("token", key);
};

export const getToken = () => {
  return localStorage.getItem("token");
};

export const deleteToken = () => {
  localStorage.removeItem("token");
};

export const getSomeWordsFromString = (str, length = 10) => {
  let result = removeTagHtml(str);
  let arrStr = str.split(" ");

  if (arrStr.length > length) {
    arrStr = arrStr.slice(0, length);
    result = arrStr.join(" ") + " ...";
  }

  return result;
};

export const removeTagHtml = str => {
  return str.replace(/<[^>]+>/g, "");
};

export const mergeArray = (...objects) => [...objects];

export const getItemById = (array, id) => {
  const index = array.findIndex(item => {
    return item.id === parseInt(id);
  });
  return {
    item: array[index],
    index
  };
};

export const removeItemById = (array, id) => {
  const index = array.findIndex(item => {
    return item.id === parseInt(id);
  });
  array.splice(index, 1);
  return {
    array,
    index
  };
};

export const getSearchParams = props => {
  const {
    location: { search }
  } = props;
  return new URLSearchParams(search);
};

export const randomString = (times = 20, number = 35, string = 36) =>
  _.times(times, () => _.random(number).toString(string)).join("");

export const convertDatatoBytes = data => {
  let type;
  let pow;
  switch (true) {
    case data < Math.pow(1024, 2):
      type = "KB";
      pow = 1;
      break;
    case data < Math.pow(1024, 3):
      type = "MB";
      pow = 2;
      break;
    case data < Math.pow(1024, 4):
      type = "GB";
      pow = 3;
      break;
    case data < Math.pow(1024, 5):
      type = "TB";
      pow = 4;
      break;
    default:
      type = "";
      pow = 0;
  }
  const size = Math.round(data / Math.pow(1024, pow), 2);

  return { size, type };
};

export const checkAtLeast = array => {
  return !_.every(array, member => _.isEmpty(member));
};

export const checkCurrentUrl = (navUrl, currentUrl, defaultUrl = "") => {
  let condition = defaultUrl === currentUrl || defaultUrl + "/" === currentUrl;
  if (navUrl !== "" && navUrl !== "/") {
    condition = currentUrl.includes(navUrl);
  }
  return currentUrl === navUrl || condition;
};

export const isDataNull = (state, fields) => {
  for (let i = 0; i < fields.length; i++) {
    if (_.isEmpty(state[fields[i]])) {
      return true;
    }
  }
  return false;
};

export const getListData = (data, fields = ["id", "name"]) => {
  let result = [];

  // eslint-disable-next-line
  data.map(item => {
    result = [
      ...result,
      {
        value: item[fields[0]],
        label: item[fields[1]]
      }
    ];
  });

  return result;
};

export const convertUndefineToEmptyString = array => {
  const result = _.map(array, item => {
    return item ? item : "";
  });
  return result;
};

export const checkScrollOnBottom = () => {
  let scrollTop =
    (document.documentElement && document.documentElement.scrollTop) ||
    document.body.scrollTop;

  let scrollHeight =
    (document.documentElement && document.documentElement.scrollHeight) ||
    document.body.scrollHeight;

  let clientHeight =
    document.documentElement.clientHeight || window.innerHeight;
  let scrolledToBottom =
    Math.ceil(scrollTop + clientHeight) >= scrollHeight && scrollTop !== 0;

  console.log("scrollTop", scrollTop);
  console.log("scrollHeight", scrollHeight);
  console.log("clientHeight", clientHeight);

  return scrolledToBottom;
};

export const getUrlPagination = (path, page) => {
  if (page === 1) return path;
  return path + "?page=" + page;
};

export const checkTokenExpired = ({
  response: {
    data: { status }
  }
}) => {
  if (status === 401 || status === 403) {
    localStorage.removeItem("token");
    window.location.href = window.location.origin + "/login";
  }
};

export const getPrevNextIdInList = ({ id, list }) => {
  const index = _.findIndex(
    list, // eslint-disable-next-line
    item => item.id == id
  );
  let isFirst = false;
  let isLast = false;
  let prevId = -1;
  let nextId = -1;
  if (index > 0) {
    prevId = list[index - 1].id;
  } else {
    isFirst = true;
  }

  if (index < list.length - 1) {
    nextId = list[index + 1].id;
  } else {
    isLast = true;
  }
  const data = {
    isFirst,
    isLast,
    prevId,
    nextId
  };
  return data;
};
