import React from "react";
import style from "./style.scss";
import classNames from "classnames";
import { Link, withRouter } from "react-router-dom";
import { checkCurrentUrl } from "util/Common";

const Index = ({ data, match: { url } }) => {
  return (
    <div className="font-weight-bold" style={{ marginBottom: "80px" }}>
      {data.map((item, i) => (
        <Link
          key={i}
          to={item.slug === "" ? `/course` : `/course/category/${item.slug}`}
          // item.slug
        >
          <div
            className={classNames(
              "text-black-70 px-3 py-2 ",
              style.item,
              checkCurrentUrl(item.slug, url, "/course") && style.active
            )}
          >
            {item.name}
          </div>
        </Link>
      ))}
    </div>
  );
};

export default withRouter(Index);
