import React, { Component } from "react";
import Form from "./Form";
import BtnNext from "pages/Instructor/components/Button/Next";
import Stepper from "pages/Instructor/components/Stepper";
import { withRouter } from "react-router-dom";
import Loading from "components/Loading";
import { getCourseRequest } from "store/Instructor/action/course";
import { connect } from "react-redux";

class Index extends Component {
  componentDidMount() {
    const { getCourseRequest, currentCourse } = this.props;
    if (!currentCourse) {
      const courseId = localStorage.getItem("courseId");
      getCourseRequest(courseId);
    }
  }

  render() {
    const courseId = localStorage.getItem("courseId");
    const { history, currentCourse } = this.props;
    if (!currentCourse) {
      return <Loading />;
    }
    return (
      <div>
        <div className="p-5">
          <Stepper currentStep={3} />
        </div>
        <Form currentCourse={currentCourse} />
        <div className="container">
          <div className="bg-white d-flex align-content-center justify-content-between p-3 mb-3">
            <BtnNext
              label="Back"
              onClick={() => {
                history.push(`/instructor/edit/${courseId}/detail`);
              }}
            />
            <BtnNext
              label="Submit course"
              onClick={() => {
                history.push(`/instructor`);
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}
const connectIndex = connect(
  ({ instructor: { currentCourse } }) => ({
    currentCourse
  }),
  { getCourseRequest }
)(Index);

export default withRouter(connectIndex);
