import { getItemById } from "util/Common";

const getItem = (array, stringId) => {
  const id = stringId.split("-").pop();
  return getItemById(array, parseInt(id));
};

// check dnd don't change position of item
export const checkDestination = (destination, source) => {
  // check null
  if (!destination) {
    return true;
  }

  // check the same position
  if (
    destination.droppableId === source.droppableId &&
    destination.index === source.index
  ) {
    return true;
  }

  return false;
};

export const getDefaultCollapse = (sections, onIndex = -1) => {
  let data = {};
  if (sections.length !== 0) {
    // eslint-disable-next-line
    sections.map(section => {
      data = {
        ...data,
        [section.id]: section.lectures.length === 0 ? true : false
      };
    });
    data[onIndex] = true;
  }

  return data;
};

export const checkInColumn = (destination, source) => {
  return destination.droppableId === source.droppableId;
};

// drag and drop in column
export const dndInColumn = (
  { destination, source, draggableId },
  sections,
  orderLectureRequest
) => {
  const { item: section, index } = getItem(sections, source.droppableId);

  // update new lectures
  const lectures = Array.from(section.lectures);

  const { startId, finishId, item: lecture } = getStartAndFinishId({
    source,
    destination,
    array: lectures,
    draggableId
  });

  // add new lecture
  lectures.splice(source.index, 1);
  lectures.splice(destination.index, 0, lecture);

  // update new section
  const newSection = {
    ...section,
    lectures: lectures
  };
  sections.splice(index, 1, newSection);

  const sectionId = section.id;
  orderLectureRequest({
    params: { currentId: startId, nextId: finishId, sectionId }
  });

  // end api

  return sections;
};

// drag and drog item go though columns
export const dndInDiffColumn = (
  { destination, source, draggableId },
  sections,
  orderLectureRequest
) => {
  const { item: startSection, index: startIndex } = getItem(
    sections,
    source.droppableId
  );

  // start block
  const startLectures = Array.from(startSection.lectures);

  // get item before move on finish block
  const { item: lecture } = getItem(startLectures, draggableId);

  // remove item with index
  startLectures.splice(source.index, 1);

  // update new Start
  const newStartSection = {
    ...startSection,
    lectures: startLectures
  };

  // finish block
  const { item: finishSection, index: finishIndex } = getItem(
    sections,
    destination.droppableId
  );

  // add new item after move on finish block
  let finishLectures = [...finishSection.lectures];
  const cloneFinishLectures = [...finishLectures];
  finishLectures.splice(destination.index, 0, lecture);

  // update new Finish block
  const newFinishSection = {
    ...finishSection,
    lectures: finishLectures
  };

  // update sections when move item
  sections.splice(startIndex, 1, newStartSection);
  sections.splice(finishIndex, 1, newFinishSection);

  // call api
  let desIndex = destination.index;
  let startId = lecture.id;
  let finishId = null;

  if (desIndex !== cloneFinishLectures.length) {
    const nextLecture = cloneFinishLectures[desIndex];
    finishId = nextLecture.id;
  }

  const sectionId = finishSection.id;
  orderLectureRequest({
    params: { currentId: startId, nextId: finishId, sectionId }
  });
  // end api

  return sections;
};

// drag and drog multi sections
export const dndColumns = (
  { destination, source, draggableId },
  sections,
  orderSectionRequest
) => {
  const { startId, finishId, item: section } = getStartAndFinishId({
    source,
    destination,
    array: sections,
    draggableId
  });
  const courseId = localStorage.getItem("courseId");

  orderSectionRequest({
    params: { currentId: startId, nextId: finishId, courseId }
  });
  sections.splice(source.index, 1);
  sections.splice(destination.index, 0, section);

  return sections;
};

const getStartAndFinishId = ({ source, destination, array, draggableId }) => {
  const { item } = getItem(array, draggableId);
  let desIndex = destination.index;
  let finishId = null;
  let startId = item.id;

  if (desIndex === array.length - 1) {
    finishId = "null";
  } else {
    if (source.index < desIndex) {
      desIndex += 1;
    }
    const nextItem = array[desIndex];
    finishId = nextItem.id;
  }
  return {
    startId,
    finishId,
    item
  };
};
