export const CREATE_COURSE_REQUEST = "CREATE_COURSE_REQUEST";
export const CREATE_COURSE_SUCCESS = "CREATE_COURSE_SUCCESS";
export const CREATE_COURSE_FAILURE = "CREATE_COURSE_FAILURE";

export const GET_COURSE_REQUEST = "GET_COURSE_REQUEST";
export const GET_COURSE_SUCCESS = "GET_COURSE_SUCCESS";
export const GET_COURSE_FAILURE = "GET_COURSE_FAILURE";

export const GET_COURSES_USER_REQUEST = "GET_COURSES_USER_REQUEST";
export const GET_COURSES_USER_SUCCESS = "GET_COURSES_USER_SUCCESS";
export const GET_COURSES_USER_FAILURE = "GET_COURSES_USER_FAILURE";

export const UPDATE_COURSE_REQUEST = "UPDATE_COURSE_REQUEST";
export const UPDATE_COURSE_SUCCESS = "UPDATE_COURSE_SUCCESS";
export const UPDATE_COURSE_FAILURE = "UPDATE_COURSE_FAILURE";

export const UPDATE_COURSE_PRICE_REQUEST = "UPDATE_COURSE_PRICE_REQUEST";
export const UPDATE_COURSE_PRICE_SUCCESS = "UPDATE_COURSE_PRICE_SUCCESS";
export const UPDATE_COURSE_PRICE_FAILURE = "UPDATE_COURSE_PRICE_FAILURE";

//create
export const createCourseRequest = (params, history) => ({
  type: CREATE_COURSE_REQUEST,
  payload: { params, history }
});

export const createCourseSuccess = course => {
  localStorage.setItem("courseId", course.id);
  return {
    type: CREATE_COURSE_SUCCESS,
    payload: { course }
  };
};

export const createCourseFailure = error => ({
  type: CREATE_COURSE_FAILURE,
  payload: { error }
});

//get by id
export const getCourseRequest = id => ({
  type: GET_COURSE_REQUEST,
  payload: { id }
});

export const getCourseSuccess = course => {
  localStorage.setItem("courseId", course.id);
  return {
    type: GET_COURSE_SUCCESS,
    payload: { course }
  };
};

export const getCourseFailure = error => ({
  type: GET_COURSE_FAILURE,
  payload: { error }
});

// get courses list
export const getCoursesUserRequest = payload => ({
  type: GET_COURSES_USER_REQUEST,
  payload
});

export const getCoursesUserSuccess = courses => {
  return {
    type: GET_COURSES_USER_SUCCESS,
    payload: { courses }
  };
};

export const getCoursesUserFailure = error => ({
  type: GET_COURSES_USER_FAILURE,
  payload: { error }
});

// update by id
export const updateCourseRequest = (id, params) => ({
  type: UPDATE_COURSE_REQUEST,
  payload: { id, params }
});

export const updateCourseSuccess = () => {
  return {
    type: UPDATE_COURSE_SUCCESS
  };
};

export const updateCourseFailure = error => ({
  type: UPDATE_COURSE_FAILURE,
  payload: { error }
});

// update PRICE by id
export const updateCoursePriceRequest = (id, params) => ({
  type: UPDATE_COURSE_PRICE_REQUEST,
  payload: { id, params }
});

export const updateCoursePriceSuccess = () => {
  return {
    type: UPDATE_COURSE_PRICE_SUCCESS
  };
};

export const updateCoursePriceFailure = error => ({
  type: UPDATE_COURSE_PRICE_FAILURE,
  payload: { error }
});
