import { takeEvery, call, put, takeLatest } from "redux-saga/effects";
import {
  // action
  setCurrentSection,
  updateSections,
  updateSectionFailure,
  updateSectionSuccess,
  createSectionFailure,
  createSectionSuccess,
  getSectionSuccess,
  getSectionFailure,
  orderSectionFailure,
  orderSectionSuccess,
  deleteSectionFailure,
  deleteSectionSuccess,

  // type
  CREATE_SECTION_REQUEST,
  UPDATE_SECTION_REQUEST,
  SET_CURRENT_SECTION,
  UPDATE_SECTIONS,
  GET_SECTION_REQUEST,
  ORDER_SECTION_REQUEST,
  DELETE_SECTION_REQUEST,
  toggleSection
} from "../action/section";
import { getCourseRequest } from "../action/course";
import {
  CreateSection,
  UpdateSection,
  GetSection,
  OrderSection,
  DeleteSection
} from "store/api/Version1";
import { removeItemById } from "util/Common";

function* watchSetCurrentSection() {
  yield takeEvery(SET_CURRENT_SECTION, setCurrentSection);
}

function* watchUpdateSections() {
  yield takeEvery(UPDATE_SECTIONS, updateSections);
}

//create
function* createSection({ payload: { params, history } }) {
  try {
    const courseId = localStorage.getItem("courseId");
    const { data } = yield call(CreateSection, courseId, params);
    yield put(createSectionSuccess(data));
    yield put(toggleSection(data.id));
    history.push(`/instructor/edit/${courseId}/content/section/${data.id}`);
  } catch (error) {
    yield put(createSectionFailure(error));
  }
}
function* watchCreateSection() {
  yield takeLatest(CREATE_SECTION_REQUEST, createSection);
}

// get
function* getSection({ payload: { id } }) {
  try {
    const { data } = yield call(GetSection, id);
    yield put(getSectionSuccess(data));
  } catch (error) {
    yield put(getSectionFailure(error));
  }
}
function* watchGetSection() {
  yield takeLatest(GET_SECTION_REQUEST, getSection);
}

// Order
function* orderSection({ payload: { params } }) {
  try {
    yield call(OrderSection, params);
    yield put(orderSectionSuccess());
  } catch (error) {
    yield put(orderSectionFailure(error));
  }
}
function* watchOrderSection() {
  yield takeLatest(ORDER_SECTION_REQUEST, orderSection);
}

// update
function* updateSection({ payload: { courseId, id, params } }) {
  try {
    yield call(UpdateSection, id, params);
    yield put(updateSectionSuccess(params));
    yield put(getCourseRequest(courseId));
  } catch (error) {
    yield put(updateSectionFailure(error));
  }
}

function* watchUpdateSection() {
  yield takeLatest(UPDATE_SECTION_REQUEST, updateSection);
}

// Delete
function* deleteSection({ payload: { id, currentCourse, history } }) {
  try {
    yield call(DeleteSection, id);
    yield put(deleteSectionSuccess());
    const courseId = localStorage.getItem("courseId");
    yield put(getCourseRequest(courseId));

    const haveSection =
      currentCourse.sections && currentCourse.sections.length > 1;
    if (haveSection) {
      const { array: sections } = removeItemById(currentCourse.sections, id);
      const section = sections[sections.length - 1];
      yield put(setCurrentSection(section));
      history.push(
        `/instructor/edit/${courseId}/content/section/${section.id}`
      );
    } else {
      yield put(setCurrentSection(null));
      history.push(`/instructor/edit/${courseId}/content/create/section`);
    }
  } catch (error) {
    yield put(deleteSectionFailure(error));
  }
}
function* watchDeleteSection() {
  yield takeLatest(DELETE_SECTION_REQUEST, deleteSection);
}

export default [
  watchSetCurrentSection(),
  watchUpdateSections(),
  watchCreateSection(),
  watchUpdateSection(),
  watchGetSection(),
  watchOrderSection(),
  watchDeleteSection()
];
