import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { getCourseRequest } from "store/Instructor/action/course";
import { getLectureRequest } from "store/Instructor/action/lecture";
import Lecture from "../components/Form/Lecture";
import Loading from "components/Loading";

class Index extends Component {
  componentDidMount() {
    const {
      currentLecture,
      match: {
        params: { lectureId }
      },
      getLectureRequest
    } = this.props;

    if (!currentLecture && lectureId) {
      getLectureRequest({ id: lectureId });
    }
  }
  render() {
    const { currentCourse } = this.props;
    if (!currentCourse) {
      return <Loading />;
    }
    return <Lecture label="Edit lecture" />;
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse } }) => ({
    currentCourse
  }),
  { getCourseRequest, getLectureRequest }
)(Index);

export default withRouter(connectIndex);
