import { call, put, takeLatest } from "redux-saga/effects";
import { delay } from "redux-saga";
import {
  GetCourse,
  EnrollCourse,
  GetCoursesByCategory,
  GetCoursesByCategorySlug,
  GetCoursesUserJoin,
  FinishLecture
} from "store/api/Version1";
import {
  getCourseSuccess,
  getCourseFailure,
  enrollCourseSuccess,
  enrollCourseFailure,
  finishLectureSuccess,
  finishLectureFailure,
  getCoursesByCategorySuccess,
  getCoursesByCategoryFailure,
  getCoursesJoinSuccess,
  getCoursesJoinFailure,
  GET_COURSE_REQUEST,
  FINISH_LECTURE_REQUEST,
  ENROLL_COURSE_REQUEST,
  GET_COURSES_BY_CATEGORY_REQUEST,
  GET_COURSES_JOIN_REQUEST
} from "./action";

// get by id
export function* getCourse({ payload: { id } }) {
  try {
    const { data } = yield call(GetCourse, id);
    yield put(getCourseSuccess(data));
  } catch (error) {
    yield put(getCourseFailure(error));
  }
}

function* watchGetCourse() {
  yield takeLatest(GET_COURSE_REQUEST, getCourse);
}

// get by id
export function* finishLecture({ payload: { id, lectureId } }) {
  try {
    yield call(FinishLecture, id, {
      lectureFinishAdd: lectureId
    });
    yield put(finishLectureSuccess(lectureId));
  } catch (error) {
    yield put(finishLectureFailure(error));
  }
}

function* watchFinishLecture() {
  yield takeLatest(FINISH_LECTURE_REQUEST, finishLecture);
}

// enroll course
export function* enrollCourse({ payload: { id, history, data } }) {
  try {
    yield call(EnrollCourse, id, data);
    yield put(enrollCourseSuccess());
    history &&
      history.push(`/course/${id}/overview`, {
        urlFrom: "enroll"
      });
  } catch (error) {
    yield put(enrollCourseFailure(error));
  }
}

function* watchEnrollCourse() {
  yield takeLatest(ENROLL_COURSE_REQUEST, enrollCourse);
}

// get by category
export function* getCoursesByCategory({
  payload: { id, params, slug, withSlug }
}) {
  try {
    let response;
    if (withSlug) {
      response = yield call(GetCoursesByCategorySlug, slug, params);
    } else {
      response = yield call(GetCoursesByCategory, id, params);
    }
    const { data } = response;
    yield delay(500);
    yield put(getCoursesByCategorySuccess(data));
  } catch (error) {
    yield put(getCoursesByCategoryFailure(error));
  }
}

function* watchGetCoursesByCategory() {
  yield takeLatest(GET_COURSES_BY_CATEGORY_REQUEST, getCoursesByCategory);
}

// get courses joined
export function* getCoursesJoin() {
  try {
    const { data } = yield call(GetCoursesUserJoin);
    yield put(getCoursesJoinSuccess(data));
  } catch (error) {
    yield put(getCoursesJoinFailure(error));
  }
}

function* watchGetCoursesJoin() {
  yield takeLatest(GET_COURSES_JOIN_REQUEST, getCoursesJoin);
}

export default [
  watchGetCourse(),
  watchGetCoursesByCategory(),
  watchGetCoursesJoin(),
  watchEnrollCourse(),
  watchFinishLecture()
];
