import React, { Component, Fragment } from "react";
import Course from "./Course";
import { getCoursesUserRequest } from "store/Instructor/action/course";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import Pagination from "components/Pagination";
import Loading from "components/Loading";
import { getSearchParams } from "util/Common";

const defaultSize = 3;
class Index extends Component {
  componentDidMount() {
    const params = getSearchParams(this.props);
    const page = params.get("page");
    const { getCoursesUserRequest } = this.props;
    getCoursesUserRequest({
      params: {
        page: page - 1 || 0,
        size: defaultSize
      }
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const nextParams = getSearchParams(nextProps);
    const params = getSearchParams(this.props);

    const page = nextParams.get("page");
    if (page !== params.get("page")) {
      nextProps.getCoursesUserRequest({
        params: {
          page: page - 1 || 0,
          size: defaultSize
        }
      });
    }
  }

  render() {
    const { courses, history } = this.props;
    if (!courses || courses.length === 0) return <Loading />;

    const coursesData = courses.content;
    return (
      <Fragment>
        <div className="py-5 bg-white">
          <div className="container py-3">
            <h2 className="text-center font-weight-normal">Your stats</h2>
            <hr className="mt-5" />
            <div className="row">
              {[1, 2, 3].map(item => (
                <div className="col-4 text-center" key={item}>
                  <h4 className="font-weight-normal">Total Revenue</h4>
                  <h3 className="text-black-70">0 VND</h3>
                </div>
              ))}
            </div>
          </div>
        </div>
        {/** my list course */}
        <div className="w-80 m-auto">
          <h2 className="text-center font-weight-normal py-5">
            My courses list
          </h2>
          <div>
            {coursesData.map((course, index) => (
              <div
                className="cursor-pointer mb-4"
                onClick={() => history.push(`/instructor/edit/${course.id}`)}
                key={index}
              >
                <Course course={course} />
              </div>
            ))}
          </div>
          <Pagination
            total={courses.totalPages}
            current={courses.page + 1}
            canPrev={courses.page !== 0}
            canNext={!courses.last}
          />
        </div>
        {/** pagination */}
      </Fragment>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { courses } }) => ({
    courses
  }),
  { getCoursesUserRequest }
)(Index);

export default withRouter(connectIndex);
