import {
  CREATE_COURSE_FAILURE,
  CREATE_COURSE_REQUEST,
  CREATE_COURSE_SUCCESS,
  GET_COURSE_FAILURE,
  GET_COURSE_REQUEST,
  GET_COURSE_SUCCESS,
  UPDATE_COURSE_FAILURE,
  UPDATE_COURSE_REQUEST,
  UPDATE_COURSE_SUCCESS,
  UPDATE_COURSE_PRICE_FAILURE,
  UPDATE_COURSE_PRICE_REQUEST,
  UPDATE_COURSE_PRICE_SUCCESS,
  GET_COURSES_USER_FAILURE,
  GET_COURSES_USER_REQUEST,
  GET_COURSES_USER_SUCCESS
} from "../action/course";
import _ from "lodash";

export const getDefaultCollapse = sections => {
  let data = {};
  if (sections.length !== 0) {
    // eslint-disable-next-line
    sections.map(section => {
      data = {
        ...data,
        [section.id]: section.lectures.length === 0 ? true : false
      };
    });
  }

  return data;
};

export default (state, action) => {
  switch (action.type) {
    // create
    case CREATE_COURSE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          params: action.payload.params,
          history: action.payload.history
        },
        isNewState: true
      };
    case CREATE_COURSE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          currentCourse: action.payload.course
        },
        isNewState: true
      };

    // get by id
    case GET_COURSE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          id: action.payload.id
        },
        isNewState: true
      };
    case GET_COURSE_SUCCESS: {
      const course = action.payload.course;
      return {
        newState: {
          ...state,
          isLoading: false,
          currentCourse: {
            ...course,
            skillId: _.map(course.skills, "id")
          },
          collapse: {
            ...getDefaultCollapse(course.sections),
            ...state.collapse
          }
        },

        isNewState: true
      };
    }

    // get COURSE USER
    case GET_COURSES_USER_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          payload: action.payload
        },
        isNewState: true
      };
    case GET_COURSES_USER_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false,
          courses: action.payload.courses
        },

        isNewState: true
      };

    // update by id
    case UPDATE_COURSE_PRICE_REQUEST:
    case UPDATE_COURSE_REQUEST:
      return {
        newState: {
          ...state,
          isLoading: true,
          id: action.payload.id,
          params: action.payload.params
        },
        isNewState: true
      };
    case UPDATE_COURSE_PRICE_SUCCESS:
    case UPDATE_COURSE_SUCCESS:
      return {
        newState: {
          ...state,
          isLoading: false
        },
        isNewState: true
      };

    case GET_COURSES_USER_FAILURE:
    case GET_COURSE_FAILURE:
    case CREATE_COURSE_FAILURE:
    case UPDATE_COURSE_FAILURE:
    case UPDATE_COURSE_PRICE_FAILURE:
      return {
        newState: {
          ...state,
          isLoading: false,
          error: action.payload.error
        },
        isNewState: true
      };
    default:
      return { newState: state, isNewState: false };
  }
};
