import React, { Component } from "react";
import Section from "./Section";
import style from "./style.scss";
import { DragDropContext, Droppable } from "react-beautiful-dnd";
import { connect } from "react-redux";
import {
  updateSections,
  orderSectionRequest
} from "store/Instructor/action/section";

import { orderLectureRequest } from "store/Instructor/action/lecture";
import { withRouter } from "react-router-dom";

import {
  dndColumns,
  dndInDiffColumn,
  dndInColumn,
  checkDestination,
  checkInColumn
} from "./common";

class Index extends Component {
  onDragEnd = result => {
    const { destination, source, type } = result;
    const {
      sections,
      updateSections,
      orderSectionRequest,
      orderLectureRequest
    } = this.props;

    // check Destination
    if (checkDestination(destination, source)) {
      return;
    }
    try {
      let newSections;
      if (type === "sections") {
        newSections = dndColumns(result, sections, orderSectionRequest);
      } else if (checkInColumn(destination, source)) {
        newSections = dndInColumn(result, sections, orderLectureRequest);
      } else {
        newSections = dndInDiffColumn(result, sections, orderLectureRequest);
      }
      updateSections(newSections);
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { sections } = this.props;

    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable droppableId="all-sections" type="sections">
          {provided => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              className={style.content}
            >
              {sections.map((section, index) => {
                const lectures = section.lectures ? section.lectures : [];
                return (
                  <Section
                    key={index}
                    lectures={lectures}
                    section={section}
                    index={index}
                  />
                );
              })}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse, currentLecture } }) => ({
    currentLecture,
    sections: currentCourse ? currentCourse.sections : [],
    currentCourse
  }),
  { updateSections, orderSectionRequest, orderLectureRequest }
)(Index);

export default withRouter(connectIndex);
