import {LOGIN, LOGOUT} from "./action";
import {getToken} from "util/Common";

const initialState = {
    isAuth: !!getToken(),
};

export default (state = initialState, action) => {
    switch (action.type) {
        case LOGIN:
            return {
                ...state,
                isAuth: true
            };
        case LOGOUT :
            return {
                ...state,
                isAuth: false
            };
        default:
            return state;
    }
};
