import React, { Suspense } from "react";
import {
  Login,
  SignUp,
  FindMentorContainer,
  JobContainer,
  ProfileMentorContainer,
  Search,
  Home,
  CourseContainer,
  InstructorContainer,
  MyCourse,
  Demo
} from "./pages";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import PrivateRoute from "util/PrivateRoute";

export default () => (
  <Router>
    <Suspense fallback={<div>Loading...</div>}>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/demo" component={Demo} />
        <Route path="/experts" component={FindMentorContainer} />

        <Route path="/login" component={Login} />
        <Route path="/sign-up" component={SignUp} />

        <Route path="/search/:topic" component={Search} />
        <Route path={`/profile`} component={ProfileMentorContainer} />

        <Route path="/jobs" component={JobContainer} />
        {/*<PrivateRoute path={`/employer`} component={ProfileContainer} />*/}
        <Route path="/course" component={CourseContainer} />
        <PrivateRoute path="/instructor" component={InstructorContainer} />
        <PrivateRoute path="/my-courses" component={MyCourse} />

        <Route path="*" component={NotFound} status={404} />
      </Switch>
    </Suspense>
  </Router>
);

const NotFound = () => {
  return <div>Not found</div>;
};
