import {
  GET_COURSE_FAILURE,
  GET_COURSE_REQUEST,
  GET_COURSE_SUCCESS,
  ENROLL_COURSE_FAILURE,
  ENROLL_COURSE_REQUEST,
  ENROLL_COURSE_SUCCESS,
  FINISH_LECTURE_FAILURE,
  FINISH_LECTURE_REQUEST,
  FINISH_LECTURE_SUCCESS,
  GET_COURSES_BY_CATEGORY_FAILURE,
  GET_COURSES_BY_CATEGORY_REQUEST,
  GET_COURSES_BY_CATEGORY_SUCCESS,
  GET_COURSES_JOIN_FAILURE,
  GET_COURSES_JOIN_REQUEST,
  GET_COURSES_JOIN_SUCCESS
} from "./action";
import { checkTokenExpired, getPrevNextIdInList } from "util/Common";
import _ from "lodash";

const initialState = {
  current: null,
  isEnrolled: false,
  isFinish: false,
  list: [],
  isLoading: true,
  error: null
};

export default (state = initialState, action) => {
  switch (action.type) {
    // get by id
    case GET_COURSE_REQUEST:
      return {
        ...state,
        isLoading: true,
        payload: action.payload
      };
    case GET_COURSE_SUCCESS: {
      const course = action.payload.course;
      let allLectures = [];
      _.forEach(course.sections, section => {
        allLectures = _.concat(allLectures, section.lectures);
      });
      let nextLectureId = _.first(allLectures).id;
      let lectureFinishList = [];
      if (course.join && course.join.lectureFinishList) {
        lectureFinishList = course.join.lectureFinishList;
        const lastLectureFinishId = _.last(lectureFinishList);

        const { nextId } = getPrevNextIdInList({
          id: lastLectureFinishId,
          list: allLectures
        });
        nextLectureId = nextId;
      }
      return {
        ...state,
        isLoading: false,
        current: {
          ...course,
          lecture: {
            list: allLectures,
            nextId: nextLectureId,
            finishList: lectureFinishList
          }
        }
      };
    }

    // enroll
    case ENROLL_COURSE_REQUEST:
      return {
        ...state,
        isLoading: true,
        payload: action.payload
      };
    case ENROLL_COURSE_SUCCESS:
      return {
        ...state,
        isLoading: false,
        isEnrolled: true
      };

    // get by category
    case GET_COURSES_BY_CATEGORY_REQUEST:
      return {
        ...state,
        isLoading: true,
        payload: action.payload
      };
    case GET_COURSES_BY_CATEGORY_SUCCESS:
      return {
        ...state,
        isLoading: false,
        list: action.payload.courses
      };

    // finish lecture
    case FINISH_LECTURE_REQUEST:
      return {
        ...state,
        isLoading: true,
        payload: action.payload
      };
    case FINISH_LECTURE_SUCCESS: {
      const {
        lecture: { finishList, list }
      } = state.current;

      const id = action.payload.id;
      const { nextId } = getPrevNextIdInList({
        id: id,
        list: list
      });
      return {
        ...state,
        isLoading: false,
        isFinish: true,
        current: {
          ...state.current,
          lecture: {
            ...state.current.lecture,
            nextId,
            finishList: [...finishList, id]
          }
        }
      };
    }
    // get courses joined
    case GET_COURSES_JOIN_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_COURSES_JOIN_SUCCESS:
      return {
        ...state,
        isLoading: false,
        list: action.payload.courses
      };

    case GET_COURSE_FAILURE:
    case ENROLL_COURSE_FAILURE:
    case GET_COURSES_BY_CATEGORY_FAILURE:
    case FINISH_LECTURE_FAILURE:
    case GET_COURSES_JOIN_FAILURE: {
      const error = action.payload.error;
      checkTokenExpired(error);

      return {
        ...state,
        isLoading: false,
        error: action.payload.error
      };
    }
    default:
      return state;
  }
};
