import React, { Component, Fragment } from "react";
import logo from "images/common/logo-code.png";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import withSizes from "react-sizes";
import screenSize from "util/ScreenSize";
import Navbar from "./Navbar";
import { Button, Fa } from "mdbreact";
import style from "./style.scss";
import classnames from "classnames";
import AuthNav from "./AuthNav";
import Sidebar from "./Sidebar";
import { getListCategoriesRequest } from "store/Category/action";

class Index extends Component {
  state = {
    sidebarOpen: false
  };
  componentDidMount() {
    window.scrollTo(0, 0);
    const { getListCategoriesRequest, list } = this.props;
    if (list.length === 0) {
      getListCategoriesRequest();
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open });
  };

  onToggleSidebar = () => {
    this.setState({ sidebarOpen: !this.state.sidebarOpen });
  };

  render() {
    const { width, isAuth } = this.props;
    const { sidebarOpen } = this.state;

    return (
      <Sidebar
        onToggleSidebar={this.onToggleSidebar}
        sidebarOpen={sidebarOpen}
        onSetSidebarOpen={this.onSetSidebarOpen}
      >
        <div
          className={classnames(
            "w-100 d-flex text-white align-items-center justify-content-between ",
            style.header
          )}
        >
          {/** nav-list */}
          <div className="d-flex align-items-center">
            <Link to="/">
              <img
                src={logo}
                alt="logo"
                width="140"
                height="20"
                style={{ marginTop: "-8px" }}
                className="mx-3"
              />
            </Link>
            {width > 1024 && <Navbar />}
            <Link to={"/jobs/search"}>
              <Button color="primary" size="md">
                Find a job
              </Button>
            </Link>
          </div>
          {/** nav-list */}

          {width <= 1024 ? (
            <div
              className="p-3 mr-3 mr-md-4 cursor-pointer"
              onClick={() => this.onToggleSidebar()}
            >
              <Fa className="text-white" size="lg" icon="bars" />
            </div>
          ) : (
            <div>
              {isAuth ? (
                <AuthNav />
              ) : (
                <Fragment>
                  <Link to={"/sign-up"}>
                    <Button color="success" size="md">
                      SIGN UP
                    </Button>
                  </Link>
                  <Link
                    to="/login"
                    className={classnames("mx-4", style.navLink)}
                  >
                    Log in
                  </Link>
                </Fragment>
              )}
            </div>
          )}
        </div>
      </Sidebar>
    );
  }
}

const connectIndex = connect(
  ({ auth: { isAuth }, category: { list } }) => ({
    isAuth,
    list
  }),
  { getListCategoriesRequest }
)(Index);

const mapSizesToProps = ({ width }) => screenSize(width);

const withSizesIndex = withSizes(mapSizesToProps)(connectIndex);

export default withSizesIndex;
