import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { getCourseRequest } from "store/Instructor/action/course";
import Lecture from "../components/Form/Lecture";

class Index extends Component {
  render() {
    return <Lecture isCreate={true} label="Add new lecture" />;
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse } }) => ({
    currentCourse
  }),
  { getCourseRequest }
)(Index);

export default withRouter(connectIndex);
