import {
  GET_LIST_SKILLS_REQUEST,
  GET_LIST_SKILLS_SUCCESS,
  GET_LIST_SKILLS_FAILURE
} from "./action";

const initialState = {
  list: [],
  error: null,
  isLoading: false
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_LIST_SKILLS_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_LIST_SKILLS_SUCCESS:
      return {
        ...state,
        isLoggined: true,
        list: action.payload.data
      };
    case GET_LIST_SKILLS_FAILURE:
      return {
        ...state,
        error: action.payload.token
      };

    default:
      return state;
  }
};

export default reducer;
