import React, { Fragment } from "react";
import _ from "lodash";
import { MDBPageItem, MDBPageNav } from "mdbreact";
import { withRouter } from "react-router-dom";
import { getUrlPagination } from "util/Common";

const Index = ({ start = 1, total, current = 0, url, history }) => (
  <Fragment>
    {_.times(total, i => {
      const index = i + start;
      const path = getUrlPagination(url, index);
      return (
        <MDBPageItem
          onClick={() => history.push(path)}
          key={index}
          active={current === index ? true : false}
        >
          <MDBPageNav className="page-link">{index}</MDBPageNav>
        </MDBPageItem>
      );
    })}
  </Fragment>
);

export default withRouter(Index);
