import React, { PureComponent } from "react";
import { Draggable } from "react-beautiful-dnd";
import classNames from "classnames";
import style from "./style.scss";
import { FaRegFileAlt, FaRegPlayCircle, FaRegTrashAlt } from "react-icons/fa";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { setCurrentSection } from "store/Instructor/action/section";
import {
  setCurrentLecture,
  deleteLectureRequest
} from "store/Instructor/action/lecture";

const getIconType = type => {
  switch (type) {
    case "VIDEO":
    case 1:
      return <FaRegPlayCircle />;
    case 2:
    case "DOCUMENT":
      return <FaRegFileAlt />;
    default:
      return <span />;
  }
};

class Index extends PureComponent {
  handleDelete = id => {
    const { deleteLectureRequest, section, history } = this.props;
    deleteLectureRequest({ id, currentSection: section, history });
  };

  render() {
    const {
      lecture,
      index,
      section,
      setCurrentLecture,
      setCurrentSection,
      currentSection,
      currentLecture,
      history
    } = this.props;
    const lectureId = lecture.id;
    const isDragDisabled = !Number.isInteger(lectureId);
    const courseId = localStorage.getItem("courseId");

    return (
      <Draggable
        draggableId={`lecture-${lectureId}`}
        index={index}
        isDragDisabled={isDragDisabled}
      >
        {provided => (
          <div
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            ref={provided.innerRef}
            className="border-top"
            onClick={e => {
              if (!isDragDisabled) {
                if (!currentSection || currentSection.id !== section.id) {
                  setCurrentSection(section);
                }
                if (!currentLecture || currentLecture.id !== lectureId) {
                  setCurrentLecture(lecture);
                }
                history.push(
                  `/instructor/edit/${courseId}/content/lecture/${lectureId}`
                );
              }
            }}
          >
            <div
              className={classNames(
                "px-4 py-3 pl-5 d-flex bg-white align-items-center justify-content-between",
                isDragDisabled ? style.itemDisable : style.item,
                currentLecture &&
                  currentLecture.id === lectureId &&
                  style.itemActiveLecture
              )}
            >
              <div>
                <div className="font-weight-normal">
                  <span className="align-text-bottom">
                    {getIconType(lecture.type)}
                  </span>
                  <span className="ml-2">
                    {isDragDisabled ? "" : `${index + 1}.`} {lecture.title}
                  </span>
                </div>
              </div>
              <div
                className="cursor-pointer mr-2"
                onClick={() => this.handleDelete(lecture.id)}
              >
                {!isDragDisabled && <FaRegTrashAlt />}
              </div>
            </div>
          </div>
        )}
      </Draggable>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentSection, currentLecture } }) => ({
    currentSection,
    currentLecture
  }),
  { setCurrentLecture, setCurrentSection, deleteLectureRequest }
)(Index);

export default withRouter(connectIndex);
