import { all } from "redux-saga/effects";
import Auth from "./Auth/saga";
import Instructor from "./Instructor/saga";
import Skill from "./Skill/saga";
import Category from "./Category/saga";
import Course from "./Course/saga";
import Lecture from "./Lecture/saga";

export default function* rootSagas() {
  yield all([
    ...Auth,
    ...Instructor,
    ...Skill,
    ...Category,
    ...Course,
    ...Lecture
  ]);
}
