import React, { Fragment, Component } from "react";
import SideBar from "../SideBar";
import style from "./style.scss";
import { getCourseRequest } from "store/Instructor/action/course";
import { connect } from "react-redux";
import BtnNext from "pages/Instructor/components/Button/Next";
import { withRouter } from "react-router-dom";
import Stepper from "pages/Instructor/components/Stepper";

export default App => {
  class Layout extends Component {
    componentDidMount() {
      const {
        getCourseRequest,
        currentCourse,
        match: {
          params: { id }
        }
      } = this.props;
      if (!currentCourse && id) {
        getCourseRequest(id);
      }
    }
    render() {
      const { currentCourse, history } = this.props;
      if (!currentCourse) {
        return <div> Loading</div>;
      }
      const courseId = localStorage.getItem("courseId");

      return (
        <Fragment>
          <div>
            <div className={style.sideBar}>
              <SideBar />
            </div>

            <div className={style.content}>
              <div className="p-5">
                <Stepper currentStep={1} />
              </div>
              <div className="px-5">
                <App />
              </div>
              <div className="bg-white d-flex align-content-center justify-content-between p-3 mx-5 mb-3">
                <BtnNext
                  label="Back"
                  onClick={() => {
                    history.push(`/instructor/edit/${courseId}`);
                  }}
                />
                <BtnNext
                  onClick={() => {
                    history.push(`/instructor/edit/${courseId}/detail`);
                  }}
                />
              </div>
            </div>
          </div>
        </Fragment>
      );
    }
  }
  const connectIndex = connect(
    ({
      instructor: { currentCourse, currentSection, currentLecture }
    }) => ({
      currentCourse,
      currentSection,
      currentLecture
    }),
    { getCourseRequest }
  )(Layout);

  return withRouter(connectIndex);
};
