import React from "react";
import TypeOne from "./TypeOne";
import TypeTwo from "./TypeTwo";
import TypeThree from "./TypeThree";
import TypeFour from "./TypeFour";
import { withRouter } from "react-router-dom";
import {
  MDBPagination,
  MDBPageItem,
  MDBPageNav,
  MDBCol,
  MDBRow
} from "mdbreact";
import { getUrlPagination } from "util/Common";

const PaginationPage = ({ total, current, url }) => {
  if (total < 10) {
    return <TypeOne url={url} total={total} current={current} />;
  } else {
    switch (true) {
      case current <= 3:
        return <TypeTwo url={url} total={total} current={current} />;
      case current <= total - 3:
        return <TypeThree url={url} total={total} current={current} />;
      default:
        return <TypeFour url={url} total={total} current={current} />;
    }
  }
};

const Pagination = ({
  total = 0,
  current = 0,
  canPrev = false,
  canNext = true,
  history,
  match: { url: path }
}) => {
  if (total === 0) return <div />;
  const urlPrev = getUrlPagination(path, current - 1);
  const urlNext = getUrlPagination(path, current + 1);
  return (
    <MDBRow className="pt-3">
      <MDBCol>
        <MDBPagination circle className="justify-content-center">
          <MDBPageItem
            disabled={!canPrev}
            onClick={() => history.push(urlPrev)}
          >
            <MDBPageNav aria-label="Previous">
              <span aria-hidden="true">Previous</span>
            </MDBPageNav>
          </MDBPageItem>

          <PaginationPage url={path} total={total} current={current} />

          <MDBPageItem
            disabled={!canNext}
            onClick={() => history.push(urlNext)}
          >
            <MDBPageNav aria-label="Next">
              <span aria-hidden="true">Next</span>
            </MDBPageNav>
          </MDBPageItem>
        </MDBPagination>
      </MDBCol>
    </MDBRow>
  );
};

export default withRouter(Pagination);
