import React, { Component } from "react";
import Video from "components/Video";
import RightSide from "../components/RightSide";
import { Link, withRouter } from "react-router-dom";
import { Fa } from "mdbreact";
import classNames from "classnames";
import style from "./style.scss";
import Layout from "../components/Layout";
import screenSize from "util/ScreenSize";
import withSizes from "react-sizes";
import Markdown from "components/Markdown";
import { TYPE_LECTURE } from "util/Constants";
import { getLectureRequest } from "store/Lecture/action";
import Loading from "components/Loading";
import { connect } from "react-redux";

class Index extends Component {
  componentDidMount() {
    let {
      match: {
        params: { lectureId }
      },
      getLectureRequest
    } = this.props;
    getLectureRequest({ id: lectureId });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    let {
      match: {
        params: { lectureId }
      },
      getLectureRequest
    } = this.props;

    const {
      match: {
        params: { lectureId: nextLectureId }
      }
    } = nextProps;

    // eslint-disable-next-line
    if (nextLectureId != lectureId) {
      getLectureRequest({ id: nextLectureId });
    }
  }

  getHeightVideo = width => {
    let height;
    switch (true) {
      case width < 576:
        height = 250;
        break;
      case width < 769:
        height = 400;
        break;
      case width < 1200:
        height = 500;
        break;
      default:
        height = 600;
    }
    return height;
  };

  render() {
    const { width, course, lecture } = this.props;
    let height = this.getHeightVideo(width);

    const isShowRightSide = true;
    if (!lecture) {
      return <Loading />;
    }

    return (
      <div className={isShowRightSide ? "container-fluid" : "container"}>
        <h1 className="h1-responsive my-5 text-center font-weight-bold">
          {course.title}
        </h1>
        <div className="row">
          <div
            className={classNames(
              isShowRightSide ? "col-12 col-xl-8 px-0" : "col-12",
              style.leftSide
            )}
          >
            {TYPE_LECTURE["VIDEO"] === lecture.type ? (
              <Video
                src={lecture.video && lecture.video.url}
                height={height}
                width="auto"
                fluid={false}
              />
            ) : (
              <div
                className={classNames(
                  "bg-white p-4 mx-md-3 ml-xl-3 mr-xl-0",
                  style.leftSide
                )}
              >
                <Markdown data={lecture.content} />
              </div>
            )}
          </div>
          {isShowRightSide && (
            <div
              className={classNames(
                "col-12 col-xl-4 pt-3 pt-xl-0",
                style.rightSide
              )}
            >
              <RightSide course={course} />
            </div>
          )}
        </div>

        <div className="text-center py-3">
          <div>
            <h5 className="h5-responsive font-weight-bold">Share with</h5>
          </div>
          <div className="btn-social box-shadow-default box-shadow-hover bg-white">
            <Link to="/facebook">
              <Fa className="text-primary" icon="facebook" />
            </Link>
          </div>
          <div className="btn-social box-shadow-default box-shadow-hover bg-white">
            <Link to="/twitter">
              <Fa icon="twitter" className="text-info" />
            </Link>
          </div>
          <div className="btn-social box-shadow-default box-shadow-hover bg-white">
            <Link to="/google">
              <Fa className="text-danger" icon="google" />
            </Link>
          </div>
        </div>
      </div>
    );
  }
}
const mapSizesToProps = ({ width }) => screenSize(width);
const withSizesIndex = withSizes(mapSizesToProps)(Index);
const connectIndex = connect(
  ({ lecture: { current } }) => ({ lecture: current }),
  { getLectureRequest }
)(withSizesIndex);
export default Layout(withRouter(connectIndex));
