import React, { Fragment } from "react";
import Card from "components/WithMentor/Course/Card";
import { Link } from "react-router-dom";

export default ({ data, url }) => (
  <Fragment>
    <div className="row ">
      {data.map((item, i) => {
        const pathname = item.join
          ? `/course/${item.id}/overview`
          : `/course/${item.id}/enroll`;
        return (
          <div
            className="col-xl-4 col-md-6 col-lg-6 col-12 mt-md-0 mb-4"
            key={i}
          >
            <Link
              to={{
                pathname,
                state: {
                  url
                }
              }}
              className="cursor-pointer"
            >
              <Card data={item} />
            </Link>
          </div>
        );
      })}
    </div>
    {data.length === 0 && (
      <h4 className="h4-responsive text-center">No course be found</h4>
    )}
  </Fragment>
);
