function NextArrow(props) {
    const {className, styles, onClick} = props;
    return (
        <img alt={'arrow right'}
             className={classnames(className, style['bounce-trans'], 'cubic-bez')}
             style={{...styles, height: '52px', width: 'auto', padding: '10px'}}
             src={require('./ic_angle_right.png')}
             onClick={onClick}/>
    );
}

function PrevArrow(props) {
    const {className, styles, onClick} = props;
    return (
        <img
            alt={'arrow right'}
            className={classnames(className, style['bounce-trans'], 'cubic-bez')}
            style={{...styles, height: '52px', width: 'auto', padding: '10px'}}
            src={require('./ic_angle_left.png')}
            onClick={onClick}/>
    );
}


