import React, { Component } from "react";
import classnames from "classnames";
import { Link, withRouter } from "react-router-dom";
import style from "./style.scss";
import {
  Button,
  Container,
  Collapse,
  Fa,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  ListGroup,
  ListGroupItem
} from "mdbreact";
import { checkCurrentUrl } from 'util/Common';
import { connect } from "react-redux";
import { logoutAction } from "store/Base/action";
import { deleteToken } from "util/Common";

const navbarItemData = [
  {
    to: "/how-it-work",
    title: "How it work"
  },
  {
    to: "/jobs/search",
    title: "Browse work"
  },
  {
    to: "/looking-for-work",
    title: "Looking for work"
  }
];

const NewDropdownItem = props => {
  return (
    <Link
      className={"dropdown-item " + style.dropdownItem}
      style={props.style}
      onClick={props.onClick || null}
      to={props.to}
    >
      {props.title}
    </Link>
  );
};

class Header extends Component {
  constructor(props) {
    super(props);
    this.toggleCollapse = this.toggleCollapse.bind(this);
    this.logout = this.logout.bind(this);
  }

  state = {
    isOpen: false
  };

  toggleCollapse = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  logout(e) {
    e.preventDefault();
    deleteToken();
    this.props.onLogout();
    this.props.history.push("/Login");
  }

  render() {
    const props = this.props;
    const { location } = props;
    // I don't understand url is not match
    //const { match: { url } } = props;
    const rightArea = props.isAuth ? (
      <div className={"mid-container height"}>
        <Dropdown className={"height"}>
          <DropdownToggle
            className={classnames(style.dropdownBtn, "height", "pl-2")}
            nav
          >
            <img
              className={"rounded-circle height mr-2"}
              src={require("images/home/ava.jpg")}
              alt="avatar"
            />
            <Fa icon={"caret-down"} />
          </DropdownToggle>
          <DropdownMenu
            right
            className={classnames("dropdown-default", style.dropdown)}
          >
            <NewDropdownItem to={"/profile"} title={"Profile"} />
            <NewDropdownItem
              style={{
                borderTop: "1px solid #e5e5e5"
              }}
              onClick={this.logout}
              to={"/logout"}
              title={"Logout"}
            />
          </DropdownMenu>
        </Dropdown>
      </div>
    ) : (
      <React.Fragment>
        {/* responsive */}
        <div className={'d-block d-lg-none'}>
          <Link to='/Login'>
            <ListGroupItem>
                Sign in
            </ListGroupItem>
          </Link>
        </div>
        <div className={"d-lg-flex d-none mid-container"}>
          <Link className={"btn btn-primary"} to={"/Login"}>
            sign in
          </Link>
        </div>
      </React.Fragment>
    );
    return (
      <div className={style.root}>
        <Container className={"d-flex height position-relative"}>
          <Link to="/jobs" className="d-flex align-items-center">
            <img
              className={"img-fluid-height"}
              style={{ height: "30px" }}
              src={require("images/common/network.png")}
              alt="LOGO"
            />
            <div className="ml-2 mt-1 logo-title">Hello work</div>
          </Link>

          <Collapse isOpen={this.state.isOpen} className={style.phoneCol}>
            <ListGroup className='d-flex flex-column d-lg-none'>
              {navbarItemData.map((item, index)=> (
                  <Link 
                    to={item.to} 
                    key={index} >
                    <ListGroupItem
                      className={checkCurrentUrl(item.to, location.pathname) ? style.active : ''}>
                    {item.title}
                    </ListGroupItem>
                  </Link>
              ))}
              {rightArea}
            </ListGroup>
          </Collapse>
          <Link className="ml-3 d-flex d-lg-none align-items-center" to={"/"}>
            <Button color="danger" size="md">
              Find a mentor
            </Button>
          </Link>
          <div className={"ml-auto height"}>
            <div className={"d-flex d-lg-none mid-container height"}>
              <div className="p-2 mr-md-3" onClick={this.toggleCollapse}>
                <Fa className="text-black" size="lg" icon="bars" />
              </div>
            </div>
            <div className={"d-lg-flex d-none height"}>
              {navbarItemData.map((ele, index) => {
                return (
                  <Link
                    key={index}
                    className={style.item + " mid-container d-flex " + (checkCurrentUrl(ele.to, location.pathname) ? style.active : '')}
                    to={ele.to}
                  >
                    {ele.title}
                  </Link>
                );
              })}
              <div className={"mid-container"}>
                <Link to={"/"}>
                  <Button color='danger'>
                    Find a mentor
                  </Button>
                </Link>
              </div>

              {rightArea}
            </div>
          </div>
        </Container>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onLogout: () => {
      dispatch(logoutAction());
    }
  };
};

const mapStateToProps = state => {
  return {
    isAuth: state.base.isAuth
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Header));
