import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Button } from "mdbreact";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { Input } from "components/Field";
import { loginRequest } from "store/Auth/action";

class Index extends Component {
  state = {
    email: "minhlevan104@gmail.com",
    password: "blockcyber@2018",
    loading: false,
    snackbar: {
      color: "",
      text: "",
      open: false
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    const { email, password } = this.state;
    const { history, loginRequest, location } = this.props;

    const prevPath = (location.state && location.state.from.pathname) || "/";
    loginRequest({
      params: {
        email,
        password
      },
      history,
      url: prevPath
    });
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  render() {
    const { email, password } = this.state;
    return (
      <form
        className={"width needs-validation"}
        onSubmit={this.handleSubmit}
        noValidate
      >
        <p className="h1 text-center font-weight-500 mb-4 text-dark">Sign in</p>

        <Input
          label="Email"
          name="email"
          placeholder="Enter email"
          handleChange={this.handleChange}
          value={email}
          required
        />
        <Input
          label="Password"
          name="password"
          type="password"
          placeholder="*******"
          handleChange={this.handleChange}
          value={password}
          required
        />

        <div className="text-center mt-3">
          <Button
            color={"primary"}
            style={{ width: "200px", maxWidth: "100%" }}
            type="submit"
          >
            Login
          </Button>
        </div>
        <Link
          className={"text-center d-block mt-3 no-hover"}
          to={"/forgot-password"}
        >
          Forgot your password?
        </Link>
        <div
          className={"width mt-3 mx-2"}
          style={{ background: "#7f92a9", height: "1px" }}
        />
        <div className={"text-center mt-3"}>
          Need an account?{" "}
          <Link
            className={"no-hover font-weight-500 text-green"}
            to={"/sign-up"}
          >
            Sign up for free
          </Link>
        </div>
        <div className="d-flex justify-content-between">
          <Link to="/" className="mid-container mt-2">
            <div className="logo-title text-dark">Codementor</div>
          </Link>
          <Link to="/jobs" className="mid-container mt-2">
            <div className="main-color logo-title">Hello work</div>
          </Link>
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  undefined,
  { loginRequest }
)(Index);

export default withRouter(connectIndex);
