import { GetLecture } from "store/api/Version1";
import { call, put, takeLatest } from "redux-saga/effects";
import {
  getLectureSuccess,
  getLectureFailure,
  GET_LECTURE_REQUEST
} from "./action";

// get
function* getLecture({ payload: { id } }) {
  try {
    const { data } = yield call(GetLecture, id);
    yield put(getLectureSuccess(data));
  } catch (error) {
    yield put(getLectureFailure(error));
  }
}
function* watchGetLecture() {
  yield takeLatest(GET_LECTURE_REQUEST, getLecture);
}

export default [watchGetLecture()];
