export const SET_CURRENT_SECTION = "SET_CURRENT_SECTION";
export const UPDATE_SECTIONS = "UPDATE_SECTIONS";

export const CREATE_SECTION_REQUEST = "CREATE_SECTION_REQUEST";
export const CREATE_SECTION_SUCCESS = "CREATE_SECTION_SUCCESS";
export const CREATE_SECTION_FAILURE = "CREATE_SECTION_FAILURE";

export const GET_SECTION_REQUEST = "GET_SECTION_REQUEST";
export const GET_SECTION_SUCCESS = "GET_SECTION_SUCCESS";
export const GET_SECTION_FAILURE = "GET_SECTION_FAILURE";

export const UPDATE_SECTION_REQUEST = "UPDATE_SECTION_REQUEST";
export const UPDATE_SECTION_SUCCESS = "UPDATE_SECTION_SUCCESS";
export const UPDATE_SECTION_FAILURE = "UPDATE_SECTION_FAILURE";

export const ORDER_SECTION_REQUEST = "ORDER_SECTION_REQUEST";
export const ORDER_SECTION_SUCCESS = "ORDER_SECTION_SUCCESS";
export const ORDER_SECTION_FAILURE = "ORDER_SECTION_FAILURE";

export const DELETE_SECTION_REQUEST = "DELETE_SECTION_REQUEST";
export const DELETE_SECTION_SUCCESS = "DELETE_SECTION_SUCCESS";
export const DELETE_SECTION_FAILURE = "DELETE_SECTION_FAILURE";

export const TOGGLE_SECTION = "TOGGLE_SECTION";

export const toggleSection = id => ({
  type: TOGGLE_SECTION,
  payload: { id }
});

export const setCurrentSection = section => {
  localStorage.setItem("sectionId", section && section.id);
  return {
    type: SET_CURRENT_SECTION,
    payload: { section }
  };
};

export const updateSections = sections => ({
  type: UPDATE_SECTIONS,
  payload: { sections }
});

//create
export const createSectionRequest = (params, history) => ({
  type: CREATE_SECTION_REQUEST,
  payload: { params, history }
});

export const createSectionSuccess = section => {
  localStorage.setItem("sectionId", section && section.id);
  return {
    type: CREATE_SECTION_SUCCESS,
    payload: { section }
  };
};

export const createSectionFailure = error => ({
  type: CREATE_SECTION_FAILURE,
  payload: { error }
});

// get by id
export const getSectionRequest = id => ({
  type: GET_SECTION_REQUEST,
  payload: { id }
});

export const getSectionSuccess = section => {
  localStorage.setItem("sectionId", section && section.id);
  return {
    type: GET_SECTION_SUCCESS,
    payload: { section }
  };
};

export const getSectionFailure = error => ({
  type: GET_SECTION_FAILURE,
  payload: { error }
});

// order
export const orderSectionRequest = payload => ({
  type: ORDER_SECTION_REQUEST,
  payload
});

export const orderSectionSuccess = () => {
  return {
    type: ORDER_SECTION_SUCCESS
  };
};

export const orderSectionFailure = error => ({
  type: ORDER_SECTION_FAILURE,
  payload: { error }
});

// update
export const updateSectionRequest = ({ courseId, id, params, history }) => ({
  type: UPDATE_SECTION_REQUEST,
  payload: { courseId, id, params, history }
});

export const updateSectionSuccess = data => {
  return {
    type: UPDATE_SECTION_SUCCESS,
    payload: { data }
  };
};

export const updateSectionFailure = error => ({
  type: UPDATE_SECTION_FAILURE,
  payload: { error }
});

// delete
export const deleteSectionRequest = payload => ({
  type: DELETE_SECTION_REQUEST,
  payload
});

export const deleteSectionSuccess = () => {
  return {
    type: DELETE_SECTION_SUCCESS
  };
};

export const deleteSectionFailure = error => ({
  type: DELETE_SECTION_FAILURE,
  payload: { error }
});
