import { call, put, takeLatest } from "redux-saga/effects";
import {
  createCourseSuccess,
  getCourseSuccess,
  getCourseFailure,
  getCoursesUserSuccess,
  getCoursesUserFailure,
  createCourseFailure,
  updateCourseSuccess,
  updateCourseFailure,
  updateCoursePriceSuccess,
  updateCoursePriceFailure,
  getCourseRequest,
  GET_COURSE_REQUEST,
  GET_COURSES_USER_REQUEST,
  CREATE_COURSE_REQUEST,
  UPDATE_COURSE_REQUEST,
  UPDATE_COURSE_PRICE_REQUEST
} from "../action/course";
import {
  CreateCourse,
  GetCourse,
  UpdateCourse,
  UpdateCoursePrice,
  GetCoursesUser
} from "store/api/Version1";
import { convertUndefineToEmptyString } from "util/Common";

//create
function* createCourse({ payload: { params, history } }) {
  try {
    const { data } = yield call(CreateCourse, params);
    yield put(createCourseSuccess(data));
    history.push(`/instructor/edit/${data.id}`);
  } catch (error) {
    yield put(createCourseFailure(error));
  }
}

function* watchCreateCourse() {
  yield takeLatest(CREATE_COURSE_REQUEST, createCourse);
}

// get by id
export function* getCourse({ payload: { id } }) {
  try {
    const { data } = yield call(GetCourse, id);
    yield put(getCourseSuccess(data));
  } catch (error) {
    yield put(getCourseFailure(error));
  }
}

function* watchGetCourse() {
  yield takeLatest(GET_COURSE_REQUEST, getCourse);
}

// get list
export function* getCoursesUser({ payload: { params } }) {
  try {
    const { data } = yield call(GetCoursesUser, params);
    yield put(getCoursesUserSuccess(data));
  } catch (error) {
    yield put(getCoursesUserFailure(error));
  }
}

function* watchGetCoursesUser() {
  yield takeLatest(GET_COURSES_USER_REQUEST, getCoursesUser);
}

// update by id
function* updateCourse({ payload: { id, params } }) {
  try {
    params.requirement = convertUndefineToEmptyString(params.requirement);
    params.target = convertUndefineToEmptyString(params.target);
    if (params.image) {
      // call image upload S3 and get url
      params.coverUrl = "https://picsum.photos/g/300/200?random";
    }
    yield call(UpdateCourse, id, params);
    yield put(updateCourseSuccess());
    const courseId = localStorage.getItem("courseId");
    yield put(getCourseRequest(courseId));
  } catch (error) {
    yield put(updateCourseFailure(error));
  }
}

function* watchUpdateCourse() {
  yield takeLatest(UPDATE_COURSE_REQUEST, updateCourse);
}

// update by id
function* updateCoursePrice({ payload: { id, params } }) {
  try {
    yield call(UpdateCoursePrice, id, params);
    yield put(updateCoursePriceSuccess());
    const courseId = localStorage.getItem("courseId");
    yield put(getCourseRequest(courseId));
  } catch (error) {
    yield put(updateCoursePriceFailure(error));
  }
}

function* watchUpdateCoursePrice() {
  yield takeLatest(UPDATE_COURSE_PRICE_REQUEST, updateCoursePrice);
}

export default [
  watchCreateCourse(),
  watchGetCourse(),
  watchUpdateCourse(),
  watchUpdateCoursePrice(),
  watchGetCoursesUser()
];
