import { call, put, takeLatest } from "redux-saga/effects";
import { GetListCategories } from "store/api/Version1";
import {
  getListCategoriesFailure,
  getListCategoriesSuccess,
  GET_LIST_CATEGORIES_REQUEST
} from "./action";

//create
function* getListCategories() {
  try {
    const { data } = yield call(GetListCategories);
    yield put(getListCategoriesSuccess(data));
  } catch (error) {
    yield put(getListCategoriesFailure(error));
  }
}

function* watchGetListCategories() {
  yield takeLatest(GET_LIST_CATEGORIES_REQUEST, getListCategories);
}
export default [watchGetListCategories()];
