import React, { Component } from "react";
import Header from "./Header";
import Footer from "./Footer";
import SearchInput from "./Search";
import Helmet from "react-helmet";
import NProgress from "nprogress";

NProgress.configure({
  minimum: 0.4,
  showSpinner: false
});

const Top = () => <div />;
export default (
  App,
  params = {
    Top: Top,
    isViewSearch: false,
    isViewFooter: true,
    isPaddingTop: true
  }
) => {
  class Layout extends Component {
    UNSAFE_componentWillUpdate() {
      // NProgress.start();
    }

    componentDidUpdate() {
      // NProgress.done();
    }

    render() {
      const { isViewSearch = false, isViewFooter = true } = params;
      const style = { paddingTop: "56px", minHeight: "100%" };
      return (
        <div style={{ position: "relative", height: "100%" }}>
          <Helmet bodyAttributes={{ style: "background-color : #e9eaec" }} />
          <Header />
          {/** end header */}

          <Top />

          <div style={style}>
            {isViewSearch && <SearchInput />}
            <App />
          </div>

          {isViewFooter && <Footer />}
        </div>
      );
    }
  }
  return Layout;
};
