import React, { Component } from "react";
import style from "./login.scss";
import { Link } from "react-router-dom";
import { Button, Container } from "mdbreact";
import withJob from "components/WithJob";
import { Helmet } from "react-helmet";
import { withRouter } from "react-router-dom";
import Form from "./Form";

class Login extends Component {
  render() {
    return (
      <div className="height d-flex">
        <Helmet bodyAttributes={{ style: "background-color : #fff" }}>
          <title>Login</title>
        </Helmet>

        <Container className={"mid-container " + style.loginContainer}>
          <Form />
        </Container>

        <div className={"d-none d-lg-block width " + style.bg}>
          <img
            className={style.object}
            src={require("./object_login.svg")}
            alt="Login"
          />
          <div style={{ margin: "100px 0 0 100px", maxWidth: "300px" }}>
            <h3 className="font-weight-bold text-white">
              Best. Work day. Ever.
            </h3>
            <h5 className="font-weight-500 text-white">
              Especially when you have the right tools to get your work done.
            </h5>
            <Link to={"ahihi"}>
              <Button className={style.btn}>Read More</Button>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default withJob(withRouter(Login), true);
