import React from "react";

const TypeJob = ({ type }) => {
  let color = "";
  switch (type) {
    case "hourly":
      color = "success";
      break;
    case "fixed price":
      color = "warning";
      break;
    case "full time":
      color = "primary";
      break;
    default:
      color = "light";
  }
  color = "bg-" + color;
  return (
    <span
      className={`font-size-13 border-radius-20 px-2 py-1 text-white ml-2 ${color}`}
    >
      {type}
    </span>
  );
};

export default TypeJob;
