export const GET_LIST_SKILLS_REQUEST = "GET_LIST_SKILLS_REQUEST";
export const GET_LIST_SKILLS_SUCCESS = "GET_LIST_SKILLS_SUCCESS";
export const GET_LIST_SKILLS_FAILURE = "GET_LIST_SKILLS_FAILURE";

export const getListSkillsRequest = () => {
  return {
    type: GET_LIST_SKILLS_REQUEST
  };
};

export const getListSkillsSuccess = data => {
  return {
    type: GET_LIST_SKILLS_SUCCESS,
    payload: { data }
  };
};

export const getListSkillsFailure = error => ({
  type: GET_LIST_SKILLS_FAILURE,
  payload: { error }
});
