import React, { PureComponent } from "react";
import { Input } from "components/Field";
import BtnNext from "../Button/Next";
import Title from "pages/Course/components/Title";
import {
  createCourseRequest,
  updateCourseRequest
} from "store/Instructor/action/course";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { Button } from "mdbreact";

class Index extends PureComponent {
  state = {
    title: ""
  };

  handleSubmit = e => {
    e.preventDefault();
    e.target.className += " was-validated";
    const {
      createCourseRequest,
      updateCourseRequest,
      history,
      currentCourse,
      isEdit
    } = this.props;
    const { title } = this.state;
    if (title) {
      const params = {
        ...currentCourse,
        title
      };
      if (isEdit) {
        const {
          match: {
            params: { id }
          }
        } = this.props;
        updateCourseRequest(id, params);
      } else {
        createCourseRequest(params, history);
      }
    }
  };

  handleChange = ({ target: { name, value } }) => {
    this.setState({
      [name]: value
    });
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.currentCourse) {
      this.setState(nextProps.currentCourse);
    }
  }

  render() {
    const { title } = this.state;
    const { history, isEdit } = this.props;
    const courseId = localStorage.getItem("courseId");
    return (
      <form
        className="needs-validation position-relative"
        noValidate
        onSubmit={this.handleSubmit}
      >
        <div className="container">
          {/** basic info */}
          <div className="bg-white px-5 py-3 mb-3">
            <Title data="1. Basic information" />
            <div className="container">
              <Input
                label="Course Title"
                name="title"
                handleChange={this.handleChange}
                value={title}
                required
              />
              <div align="center">
                <Button type="submit">Save</Button>
              </div>
              {/** end basic info */}
            </div>
          </div>

          {/** btn next */}
          {isEdit && (
            <div className="bg-white d-flex align-content-center justify-content-between p-3 mb-3">
              <BtnNext
                label="Back"
                onClick={() => {
                  history.push(`/instructor`);
                }}
              />
              <BtnNext
                onClick={() => {
                  history.push(`/instructor/edit/${courseId}/content`);
                }}
              />
            </div>
          )}
        </div>
      </form>
    );
  }
}

const connectIndex = connect(
  ({ instructor: { currentCourse } }) => ({ currentCourse }),
  { createCourseRequest, updateCourseRequest }
)(Index);

export default withRouter(connectIndex);
